<template>
	<div>
		<el-container>
			<el-header>
				<headerNav />
			</el-header>
			<el-main>
				<div class="left">
					<div class="title">欢迎来到美惑</div>
					<div class="content" v-show="loginTips">
						美惑是现在年经人的生活方式平台，由我们团队一起创作的。美惑以“分享和发现世界的精彩”为使命， 用户可以通过短视频、图文等形式记录生活点滴，分享生活方式，并基于兴趣形成互动。
					</div>
					<div v-show="regTips">
						未注册的手机验证通过后将自动注册
					</div>
					<div v-show="forgetPwdTips">
						忘记密码
					</div>
				</div>
				<div class="right" v-show="loginShow">
					<el-tabs :stretch="true" v-model="activeName" @tab-click="handleClick">
						<el-input maxlength="11" v-model="phone" placeholder="请输入手机号码"></el-input>
						<el-tab-pane label="账户密码登录" name="first">
							<el-input type="password" v-model="pass" minlength="6" placeholder="请输入密码"></el-input>
						</el-tab-pane>
						<el-tab-pane label="手机号登录" name="second">
							<el-input maxlength="6" v-model="checkCode" placeholder="请输入验证码">
								<template slot="append">
									<el-button @click="validateBtn" style="color: #409EFF;">{{btnTitle}}</el-button>
								</template>
							</el-input>
						</el-tab-pane>
						<el-button type="primary" class="loginBut" @click="sub">{{loginText}}</el-button>
					</el-tabs>
					<div class="forgetPwd" @click="forgetPwd">忘记密码？</div>
				</div>
				<div class="right" v-show="forgetPwdShow">
					<div class="title">手机重置密码</div>
					<el-input maxlength="11" v-model="FPphone" placeholder="请输入手机号码" />
					<el-input maxlength="6" v-model="FPcheckCode" placeholder="请输入验证码">
						<template slot="append">
							<el-button @click="validateFPBtn" style="color: #409EFF;">{{PFbtnTitle}}</el-button>
						</template>
					</el-input>
					<el-input type="password" v-model="newPass" minlength="6" placeholder="请输入新密码"></el-input>
					<el-button type="primary" class="loginBut" @click="update">修改</el-button>
					<div class="login-type">
						<div class="pwd-phone-login" @click="PwdLogin">密码登录</div>
						<div class="pwd-phone-login" @click="PhoneLogin">手机登录</div>
					</div>
				</div>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	import headerNav from "../components/headerNav.vue";
	import {
		Form,
		Button,
		Input,
		FormItem,
		Container,
		Header,
		Main,
		Tabs,
		TabPane,
		Message
	} from 'element-ui';
	export default {
		components: {
			headerNav,
			[Form.name]: Form,
			[Button.name]: Button,
			[Input.name]: Input,
			[FormItem.name]: FormItem,
			[Container.name]: Container,
			[Header.name]: Header,
			[Main.name]: Main,
			[Tabs.name]: Tabs,
			[TabPane.name]: TabPane,
			[Message.name]: Message,
		},
		data() {
			return {
				activeName: 'first',
				btnTitle: '获取验证码',
				PFbtnTitle: '获取验证码',
				phone: '',
				pass: '',
				checkCode: '',
				loginText: '登录',
				loginTips: true,
				regTips: false,
				forgetPwdTips:false,
				loginShow: true,
				forgetPwdShow: false,
				// 忘记密码
				FPphone: '',
				FPcheckCode: '',
				newPass: ''
			};
		},
		methods: {
			// 倒计时
			validateBtn() {
				let time = 6;
				let timer = setInterval(() => {
					// console.log(time);
					if (time == 0) {
						clearInterval(timer);
						this.btnTitle = "重新发送";
					} else {
						this.btnTitle = time + '秒后重试';
						time--
					}
				}, 1000)
			},
			// 忘记密码 手机验证码
			validateFPBtn(){
				let phoneTime = 6;
				let timer = setInterval(() => {
					// console.log(time);
					if (phoneTime == 0) {
						clearInterval(timer);
						this.PFbtnTitle = "重新发送";
					} else {
						this.PFbtnTitle = phoneTime + '秒后重试';
						phoneTime--
					}
				}, 1000)
			},
			// 登录
			sub() {
				if (this.activeName == "first") {
					if (this.phone == "") {
						Message.error('请输入手机号码');

					} else if (this.pass == "") {
						Message.error('请输入登录密码');
					} else if (!/^1[3456789]\d{9}$/.test(this.phone)) {
						Message.error('手机号格式错误');
					} else if (!/^.{6,}$/.test(this.pass)) {
						Message.error('密码格式错误');
					} else {
						this.loginText = "登陆中...";
						setTimeout(() => {
							this.$router.push('/my_account');
							sessionStorage.setItem("loginK", true);
						}, 1000);
					}
				}
				if (this.activeName == "second") {
					if (this.phone == "") {
						Message.error('请输入手机号码');

					} else if (!/^1[3456789]\d{9}$/.test(this.phone)) {
						Message.error('手机号格式错误');
					} else if (this.checkCode == "") {
						Message.error('请输入验证码');
					} else if (!/^\d{6}$/.test(this.checkCode)) {
						Message.error('请输入六位验证码');
					} else {
						this.loginText = "登陆中...";
						setTimeout(() => {
							this.$router.push('/my_account');
						}, 1000);
					}
				}
			},
			// 标签切换
			handleClick() {
				if (this.activeName == "first") {
					this.loginTips = true;
					this.regTips = false;
				}
				if (this.activeName == "second") {
					this.loginTips = false;
					this.regTips = true;
				}
			},
			// 忘记密码
			forgetPwd() {
				// 显示忘记密码模块
				this.loginShow = false;
				this.forgetPwdShow = true;	
				// 右边显示的内容
				this.loginTips = false;
				this.regTips = false;
				this.forgetPwdTips = true;
			},
			// 修改密码
			update() {
				if (this.FPphone == "") {
					Message.error('请输入手机号码');

				} else if (!/^1[3456789]\d{9}$/.test(this.FPphone)) {
					Message.error('手机号格式错误');
				} else if (this.FPcheckCode == "") {
					Message.error('请输入验证码');
				} else if (!/^\d{6}$/.test(this.FPcheckCode)) {
					Message.error('请输入六位验证码');
				} else if (this.pass == "") {
					Message.error('请输入新密码');
				} else if (!/^.{6,}$/.test(this.newPass)) {
					Message.error('新密码格式错误');
				} else {
					Message.success('密码重置成功');
					setTimeout(() => {
						this.PwdLogin();
					}, 3000);
				}
			},
			// 密码登录
			PwdLogin() {
				this.loginShow = true;
				this.forgetPwdShow = false;
				this.activeName = "first";
				// 右边显示的内容
				this.loginTips = true;
				this.regTips = false;
				this.forgetPwdTips = false;
			},
			// 手机登录
			PhoneLogin() {
				this.loginShow = true;
				this.forgetPwdShow = false;
				this.activeName = "second";
				// 右边显示的内容
				this.loginTips = false;
				this.regTips = true;
				this.forgetPwdTips = false;
			}
		}
	}
</script>

<style scoped>
	/* 主体内容 */
	.el-main {
		width: 911px;
		margin: 100px auto 0;
		background-color: #fff;
		box-shadow: 0 0 20px 0 rgba(51, 51, 51, 0.20);
		display: flex;
		height: 400px;
		padding: 0;
	}


	/* 左边 */
	.el-main .left {
		width: 50%;
		background-color: #57BEC3;
		color: #fff;
		text-align: center;
	}

	/* 欢迎语 */
	.left .title {
		height: 60px;
		line-height: 60px;
		font-size: 20px;
	}

	.left .content {
		width: 80%;
		margin: 10px auto 0;
		text-align: left;
		line-height: 30px;
	}

	/* 右边 */
	.el-main .right {
		width: 50%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}



	/* 标签切换 */

	.right>>>.el-tabs__header {
		display: flex;
		justify-content: center;
		margin-bottom: 40px;
	}

	.right>>>.el-tabs__nav-wrap {
		width: 100%;
	}

	/* 输入框 */
	.el-input {
		width: 350px;
		margin-bottom: 20px;
	}

	/* 登录按钮 */
	.loginBut {
		margin-top: 10px;
		width: 350px;
		font-size: 18px;
	}

	/* 忘记密码 */
	.forgetPwd {
		margin-top: 20px;
		color: #005980;
		cursor: pointer;
	}

	.right .title {
		margin-bottom: 30px;
		font-size: 20px;
		font-weight: 600;
		color: #333333;
	}

	/* 登录方式 */
	.login-type {
		display: flex;
		justify-content: space-between;
		width: 100%;
		margin-top: 20px;
		width: 350px;
	}
	.login-type .pwd-phone-login {
		cursor: pointer;
		color: #005980;
	}
</style>
