                    <el-row type="flex" justify="space-between">
                        <el-col :span="11">
                            <el-carousel height="350px">
                                <el-carousel-item v-for="item in 4" :key="item">
                                    <img :src="commodityInfo.img" width="100%" height="100%" />
                                </el-carousel-item>
                            </el-carousel>
                        </el-col>
                        <el-col :span="12">
                            <div class="products">
                                <el-row>
                                    <el-col :span="6">
                                        <i class="el-icon-s-shop"></i>{{commodityInfo.shopName}}</el-col>
                                    <el-col :span="6">
                                        <i class="el-icon-star-on"></i>关注店铺</el-col>
                                    <el-col :span="6">
                                        <i class="el-icon-circle-check"></i>喜欢</el-col>
                                    <el-col :span="6">
                                        <i class="el-icon-share"></i>分享</el-col>
                                </el-row>

                            </div>
                            <p>{{commodityInfo.description}}</p>
                            <div class="price_favorable">
                                <el-row>
                                    <el-col :span="3">美惑价</el-col>
                                    <el-col :span="17">
                                        <strong>￥{{commodityInfo.currentprice}}</strong>
                                        <del>￥{{commodityInfo.costprice}}</del>
                                        <span>降价通知</span>

                                    </el-col>
                                    <el-col :span="4">
                                        累积评价
                                        <p>{{comment}}+</p>
                                    </el-col>
                                </el-row>
                            </div>

                            <el-row class="weight">
                                <el-col :span="3">重量</el-col>
                                <el-col :span="13">{{commodityInfo.weight}}kg</el-col>
                            </el-row>
                            <el-divider></el-divider>

                            <el-row class="kouwei">
                                <el-col :span="4">选择口味</el-col>
                                <el-col :span="20">
                                    <div v-for="(coy,k) in taste" :key="k" @click="frame(k)">
                                        <el-col :span="11" class="choose" v-bind:class="{choose2: coy.selectivity}">
                                            <el-col :span="5">
                                                <img :src="commodityInfo.img" width="40px" height="37px" />
                                            </el-col>
                                            <el-col :span="19">{{coy.flavor}}</el-col>
                                        </el-col>
                                    </div>

                                </el-col>
                            </el-row>
                            <el-divider></el-divider>
                            <el-row class="bnr" type="flex" justify="space-between">
                                <el-col :span="7">
                                    <el-input-number v-model="commodityInfo.count" :min="1" :max="10" label="描述文字"></el-input-number>
                                </el-col>
                                <el-col :span="7">
                                    <el-button type="warning"  @click="addCar">加入购物车</el-button>
                                </el-col>
                                <el-col :span="7">
                                    <router-link to="/commodity_payment">
                                        <el-button type="danger">立即购买</el-button>
                                    </router-link>
                                </el-col>
                            </el-row>

                        </el-col>
                    </el-row>
                </div>
                <div class="pro_detail">
                    <el-tabs v-model="activeName" type="card">
                        <el-tab-pane label="商品介绍" name="first">
                            <p>品牌：<span> 美惑</span></p>
                            <el-row class="spjs">
                                <el-col :span="6">
                                    <p> 商品名称：<span>舌里蛋黄酥500g/箱</span></p>
                                    <p> 是否添加辣：<span>添加辣</span></p>
                                </el-col>
                                <el-col :span="6">
                                    <p> 商品编号：<span>100010894462</span></p>
                                    <p> 类别：<span>老友粉</span></p>
                                </el-col>
                                <el-col :span="6">
                                    <p> 商品毛重：<span>1.5kg</span></p>
                                    <p> 包装：<span>一整箱</span></p>
                                </el-col>
                                <el-col :span="6">
                                    <p> 商品产地：<span>河南省新乡市</span></p>

                                </el-col>
                            </el-row>
                            <div class="msimg">
                                <img v-for="(img,i) in miaoshu" :key="i" :src="img" />
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="规格与包装" name="second">
                            <el-row>
                                <el-col :span="6">
                                    主体 
                                </el-col>
                                <el-col :span="6">
                                    生产许可证号
                                </el-col>
                                <el-col :span="6">
                                    SC12441078201562
                                </el-col>
                            </el-row>

                        </el-tab-pane>

                        <el-tab-pane label="售后保障" name="fourth">
                            <div class="after_information">
                                <p>厂家服务</p>
                                <span>本产品全国联保，享受三包服务，质保期为：无质保</span>
                            </div>
                            <div class="after_information">
                                <p>美惑承诺</p>
                                <span>美惑平台卖家销售并发货的商品，由平台卖家提供发票和相应的售后服务。请您放心购买！
                                    注：因厂家会在没有任何提前通知的情况下更改产品包装、产地或者一些附件，本司不能确保客户收到的货物与商城图片、产地、附件说明完全一致。只能确保为原厂正货！并且保证与当时市场上同样主流新品一致。若本商城没有及时更新，请大家谅解！</span>
                            </div>
                            <div class="after_information">
                                <p>无忧退货</p>
                                <span>客户购买京东自营商品7日内（含7日，自客户收到商品之日起计算），在保证商品完好的前提下，可无理由退货。（部分商品除外，详情请见各商品细则）</span>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </el-main>
        </el-container>
    </div>
</template>

<script>
    import headerNav from "../../components/headerNav.vue";
    import {
        row,
        col,
        container,
        header,
        main,
        carousel,
        CarouselItem,
        divider,
        InputNumber,
        button,
        TabPane,
        tabs,
    } from 'element-ui';
    export default {
        data() {
            return {
                // num: 1,
                activeName: 'first',
                id:this.$route.query.id, 
                commodityInfo:[],
                comment: 2000,
                taste: [{
                        selectivity: true,
                        img: require('../../assets/images/commodityCenter/techan2.jpg'),
                        flavor: '原味'
                    },
                    {
                        selectivity: false,
                        img: require('../../assets/images/commodityCenter/techan2.jpg'),
                        flavor: '中辣'
                    },
                    {
                        selectivity: false,
                        img: require('../../assets/images/commodityCenter/techan2.jpg'),
                        flavor: '微辣'
                    },
                    {
                        selectivity: false,
                        img: require('../../assets/images/commodityCenter/techan2.jpg'),
                        flavor: '超辣'
                    }
                ],
                miaoshu: [
                    require('../../assets/images/commodityCenter/miaoshu1.jpg'),
                    require('../../assets/images/commodityCenter/miaoshu2.jpg'),
                    require('../../assets/images/commodityCenter/miaoshu3.jpg'),
                ], 
            } 
        },
        components: {
            headerNav,
            [row.name]: row,
            [col.name]: col,
            [container.name]: container,
            [header.name]: header,
            [main.name]: main,
            [carousel.name]: carousel,
            [CarouselItem.name]: CarouselItem,
            [divider.name]: divider,
            [InputNumber.name]: InputNumber,
            [button.name]: button,
            [tabs.name]: tabs,
            [TabPane.name]: TabPane,

        }, 
        created(){
            let id = this.id;
            this.commodityFun(id)
        },
        methods: {
            frame(k) {
                for (let i = 0; i < this.commodity.taste.length; i++) {
                    if (k == i) {
                        this.commodity.taste[k].selectivity = true
                    } else {
                        this.commodity.taste[i].selectivity = false
                    }
                }
            },
            commodityFun(id){
                let commodity = this.$store.state.commodity_List
                let commodityArr =[];
                commodity.some(item=>{
                    if(item.id==id){  
                        commodityArr=item; 
                        return true;
                    }
                })
                this.commodityInfo=commodityArr;
            },
             // 加入购物车
            addCar(){
                var carInfo={
                    shopName:this.commodityInfo.shopName, 
                    shopId:this.commodityInfo.shopId, 
                    shopIcon:this.commodityInfo.shopIcon, 
                    id:this.commodityInfo.id,
                    title:this.commodityInfo.description,
                    img:this.commodityInfo.img,
                    currentprice:this.commodityInfo.currentprice,
                    count:this.commodityInfo.count,
                    checked: false 
                }; 
        
                this.$store.commit('addCart',carInfo) 
            }, 
        },
        
    }
</script>

