        width: 1200px;

        margin: 30px auto 0;
        padding: 0;
    }

    #commodity_foodpage .goodsby>>>.el-carousel__indicator--horizontal {
        display: none;
    }

    .goodsby p {
        margin-bottom: 10px;
    }

    #commodity_foodpage .products {
        padding-top: 10px;
        font-size: 16px;
        line-height: 0;
        margin-bottom: 10px;
    }

    #commodity_foodpage .products i {
        font-size: 25px;
        padding-right: 5px;
        vertical-align: middle;

    }

    #commodity_foodpage .kouwei .el-col-4 {
        padding-top: 15px;
    }

    #commodity_foodpage .el-divider--horizontal {
        margin: 5px 0;
    }

    #commodity_foodpage .price_favorable .el-col-3 {
        padding-top: 10px;
