    }

    #commodity_foodpage .price_favorable .el-col-17 strong {
        color: red;
        font-size: 24px;
    }

    #commodity_foodpage .price_favorable .el-col-17 del {
        margin: 0 5px;
        color: gray;
    }

    #commodity_foodpage .price_favorable .el-col-17 span {
        color: deepskyblue;
    }

    #commodity_foodpage .weight .el-col-3 {
        letter-spacing: 10px;
    }

    #commodity_foodpage .goodsby .choose {
        border: 1px solid gainsboro;
        height: 39px;
        margin: 10px 15px 0 0;
    }

    #commodity_foodpage .goodsby .choose2 {
        border: 1px solid red;

    }

    #commodity_foodpage .goodsby .choose:hover {
        border: 1px solid red;
    }

    #commodity_foodpage .goodsby .el-col-5 img {
        font-size: 0;
    }

    #commodity_foodpage .goodsby .choose .el-col-19 {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        line-height: 40px;
    }

    #commodity_foodpage .goodsby .bnr {
        margin-top: 15px;
    }

    #commodity_foodpage .goodsby .bnr .el-button {
        width: 90%;
    }

    #commodity_foodpage .spjs {
        margin: 15px 0;
    }

    #commodity_foodpage .pro_detail {
        margin: 50px auto;

    }

    #commodity_foodpage .msimg {
        width: 80%;
        margin: auto;
    }

    #commodity_foodpage .pro_detail .after_information {
        margin: 30px 0;
    }
</style>
