<template>
    <div id="commodity_index">
        <!--  头部 -->
        <el-container>
            <el-header>
                <headerNav />
            </el-header>
        </el-container>
        <!-- 内容 -->
        <div class="commodity_content">
            <el-row>
                <!-- 左边导航 -->
                <div class="commodity_content_left">
                    <!-- 右边内容 -->
                    <el-tabs :tab-position="tabPosition">
                        <el-tab-pane :label="the.title" v-for="(the,k) in commodity_List" :key="k">
                            <div v-for="(list,i) in the.list" :key="i" class="the">
                                <div @click="next(list.bnt,list.id)">
                                    <img :src="list.img" width="100%" height="200px" />
                                    <p class="describe">{{list.description}}</p>
                                </div>
                                <div id="commodity">￥{{list.currentprice}}
                                    <del>{{list.costprice}} </del>
                                    <el-row>
                                        <el-button type="primary" round @click="order">{{list.bnt}}</el-button>
                                    </el-row>
                                </div>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div> 
            </el-row> 
        </div>
        <el-footer>
            <bottom/>
        </el-footer>
    </div>
</template>

<script>
    import headerNav from "../../components/headerNav.vue";
    import bottom from "../../components/bottom.vue";
    import shopPurchases from "../../utils/carInfo.js";
    import {
        row,
        col,
        container,
        header,
        tabs,
        TabPane,
        Button,
        Footer,
    } from 'element-ui';
    export default {
        data() {
            return {
                tabPosition: 'left',
                commodity_List: []
            };
        },
        components: {
            headerNav,
            bottom,
            [row.name]: row,
            [col.name]: col,
            [container.name]: container,
            [header.name]: header,
            [tabs.name]: tabs,
            [TabPane.name]: TabPane,
            [Button.name]: Button,
            [Footer.name]: Footer,
        },
        created(){
            this.commodity_List = shopPurchases.shopPurchases;  
            let commodityList = [];
			this.commodity_List.map(mapItem => {
				if (commodityList.length == 0) {
					commodityList.push({  
						title: mapItem.title, 
						list: [mapItem] 
					})
				}else {
					let res = commodityList.some(item=> {//判断相同title，有就添加到当前项
						if (item.title == mapItem.title) {
							item.list.push(mapItem)
							return true
						}
					})
					if (!res) {//如果没找title添加一个新对象
					commodityList.push({ 
						title: mapItem.title,
						list: [mapItem] })
					}
                }
                for(var i =0;i<commodityList.length;i++){
                    let lists = commodityList[i].list;
                    lists.some(item=> {//判断相同title，有就添加到当前项
						if (item.type =="specialty" || item.type =="Keepsake") { 
							item.bnt="立即购买"
                        }else{
                             item.bnt="立即预约"
                        }
					})
                }
                this.commodity_List =commodityList 
            })
            this.$store.commit('commodityList',shopPurchases.shopPurchases)
        },
        methods: {
            next(type,id) { 
                if(type=="立即购买"){ 
                    this.$router.push({
                        path:'/commodity_foodpage',
                        query:{
                            id:id
                        } 
                    })   
                }else{
                    this.$router.push({
                        path:'/commodity_details',
                        query:{
                            id:id
                        } 
                    })
                } 
               
            },
            order() { 
                this.$router.push('/commodity_order')
            }
        }
    }
</script>
<style scoped>
#commodity_index .el-footer {
    padding: 0 0;

}
    #commodity_index {
        width: 100%;
        height: 100%;
    }

    /* 内容 */
    .commodity_content {
        width: 1450px;
        margin: 100px auto 0;

    }

    /* 左边导航 */

    /* .commodity_content_left {
        
        height: 500px;
        
    }
   */
    /* 右边内容 */
    .commodity_content_right {
        width: 100%;


    }

    /*  用户管理 */
    .el-tab-pane {
        height: 100%;
        /* border: 1px solid red; */
        display: flex;
        flex-wrap: wrap;
        
    }

    .el-tab-pane .the {
        width: 24%;
        margin:0 0  20px 11px;
        
    }

    .el-tab-pane .describe {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
    }

    .el-tab-pane del {
        color: gray;
    }

    #commodity {
        display: flex;
        justify-content: space-between;
        line-height: 50px;
        color: red;
        width: 90%;

    }

    #commodity .el-button.is-round {
        padding: 1px 23px;
        height: 30px;
    }

    #commodity .el-button--primary {
        background-color: #57BEC3;
        border-color: #57BEC3;
    }
</style>
<style>
    .commodity_content_left .el-tabs--left .el-tabs__item.is-left {
        text-align: center;
        height: 60px;
        font-size: 15px;
        font-weight: 600;
        width: 150px;
    }

    .commodity_content_left .el-tabs__item:hover {
        color: #57BEC3;
    }

    .commodity_content_left .el-tabs__item.is-active {
        color: #57BEC3;
    }

    .commodity_content_left .el-tabs__active-bar {
        background-color: #57BEC3;
    }
</style>
