import Vue from 'vue'
import App from './App.vue'

 
import VueRouter from 'vue-router';
import router from './router/router.js';
Vue.use(VueRouter);

 
// 引入vuex
import Vuex from 'vuex' 
Vue.use(Vuex) 
var car = JSON.parse(localStorage.getItem('car')||'[]')

var commodity_List = JSON.parse(localStorage.getItem('commodity_List')||'[]')
const store = new Vuex.Store({
	state: {  
		car:car,
		commodity_List:commodity_List,
	},
	mutations: { 
		commodityList(state,commodity_List){
			state.commodity_List=commodity_List; 
			localStorage.setItem('commodity_List',JSON.stringify(state.commodity_List))

		},
		// 添加购物车
		addCart(state,carInfo){
			var flag = false 
			state.car.some(item=>{
				if(item.id==carInfo.id){
					item.count +=parseInt(carInfo.count)
					flag=true
					return true
				}
			})
			if(!flag){
				state.car.push(carInfo)
			} 
			localStorage.setItem('car',JSON.stringify(state.car))
		},
		// 购物车信息修改
		updateCarInfo(state,carInfo){ 
			state.car.some(item=>{
				if(item.id==carInfo.id){
					item.count =parseInt(carInfo.count) 
					return true
				}
			})
			localStorage.setItem('car',JSON.stringify(state.car))
		},
		// // 修改购物车复选框的状态
		// updateCarChecked(state,e){
		// 	state.car.some(item=>{
		// 		if(item.id==e.id){
		// 			item.checked=e.checked
		// 		}
		// 	})
		// 	localStorage.setItem('car',JSON.stringify(state.car))
		// },
		// 购物车删除列表
		removelist(state,id){
			state.car.some((item,i)=>{
				if(item.id==id){
					state.car.splice(i,1)
					return true;
				}
			})
			localStorage.setItem('car',JSON.stringify(state.car))
		},
		// 购物车信息删除
		removeChecked(state,checked){
			state.car.some((item,i)=>{
				if(item.checked==checked){
					state.car.splice(i,1)
					return true;
				}
			})
			localStorage.setItem('car',JSON.stringify(state.car))
		},
		// 清空购物车
		emptyShopCart(state){
			state.car=[]
			localStorage.setItem('car',JSON.stringify(state.car))
		}
	},
	
	getters:{
		getAllCount(state){
			var c=0;
			state.car.forEach(item=>{
				c +=item.count
			})
			return c
		}
	}
})

//图标
import "./assets/font/Community/iconfont.css";

Vue.config.productionTip = false

new Vue({
	el: '#app',
	router, 
	store,
	render: h => h(App),
}).$mount('#app')
