			}
        },
        getEmo(index) {
            var textArea = document.getElementById("textarea");
            function changeSelectedText(obj, str) {
                if (window.getSelection) {
                    // 非IE浏览器
                    textArea.setRangeText(str);
                    // 在未选中文本的情况下，重新设置光标位置
                    textArea.selectionStart += str.length;
                    textArea.focus();
                } else if (document.selection) {
                    // IE浏览器
                    obj.focus();
                    var sel = document.selection.createRange();
                    sel.text = str;          
                }
            }
            changeSelectedText(textArea, this.faceList[index]);
                this.todo = textArea.value;          
            // 要同步data中的数据
            //  console.log(this.faceList[index]);
            return;
            
        },

        otherFun(aid){
            this.elseDetails=elseDetails;
            for(var i in this.elseDetails){
                    let lists= this.elseDetails[i];   
                    let  NewestArr=[];  
         
                            
                    for(var k in lists){ 
                        let item=lists[k];

                        if(item.type=="NewestInfo") { //最新
                            console.log(item.type + item.userID);                    
                            if(item.type + item.userID == aid){                       
                                NewestArr=item;                                   
                                                
                            } 
                        }
                        if(item.type=="NativeInfo") {   //特产                  
                            if(item.type + item.userID == aid){                       
                                NewestArr=item;                                   
                                                
                            } 
                        }
                        if(item.type=="FoodInfo") {   //美食                  
                            if(item.type + item.userID == aid){                       
                                NewestArr=item;                                   
                                                
                            } 
                        }
                        if(item.type=="KeepsakeInfo") {   //纪念品                  
                            if(item.type + item.userID == aid){                       
                                NewestArr=item;                                   
                                                
                            } 
                        }
                        if(item.type=="TravelInfo") {   //周边游                  
                            if(item.type + item.userID == aid){                       
                                NewestArr=item;                                   
                                                
                            } 
                        }
                        if(item.type=="MinsuInfo") {   //民宿                  
                            if(item.type + item.userID == aid){                       
                                NewestArr=item;                                   
                                                
                            } 
                        }
                    }   


                    this.Newestlist=NewestArr;  
                 
        
            }
                  
        }
        
       
    },
    
     mounted() {
        //  获取当前的时间
            this.date = new Date();
            var now =  this.date;
            var month = now.getMonth() + 1; //月                 
            var day = now.getDate(); //日          
            var tomonth=  "0"+ month + "-" + day;          
            this.date=tomonth;
        // 表情包
        for (let i in appData) {
            this.faceList.push(appData[i].char);              
        }

     
      
    },
   

}
