import Vue from "vue";
import VueRouter from "vue-router";

// 引入 组件
import index from "../views/index.vue"; //首页

import Tourism from "../views/Community/Tourism.vue"; //旅游社区
import otherdetails from "../views/Community/otherdetails.vue";//旅游社区-》发布者作品详情
import indexdetails from "../views/Community/indexdetails.vue";//首页-》发布者作品详情
import otherMeans from "../views/Community/otherMeans.vue";//旅游社区-》发布者作品资料
import issueOpus from "../views/Community/issueOpus.vue";//旅游社区-》发布作品
import follow from "../views/Community/follow.vue";//旅游社区-》关注

import commodity_index from "../views/commodityCenter/commodity_index.vue"; //商品中心
import commodity_details from "../views/commodityCenter/commodity_details.vue"; //商品中心
import commodity_order from "../views/commodityCenter/commodity_order.vue"; //订单
import commodity_foodpage from "../views/commodityCenter/commodity_foodpage"; /* 美食商品详情*/
import commodity_payment from "../views/commodityCenter/commodity_payment.vue"; /* 支付页面*/
import commodity_accomplish from "../views/commodityCenter/commodity_accomplish.vue"; /* 支付成功页面*/


import AboutUs from "../views/aboutus/AboutUs.vue"; //关于我们
import Download from "../views/Download/Download.vue"; //app下载
import login from "../views/login.vue"; //登录
import my_account from "../views/my_account.vue"; //我的账户
import my_order from "../views/my_order.vue"; //我的订单
import my_set from "../views/my_set.vue"; //个人设置

import shopCart from "../views/shopCart.vue";//购物车


Vue.use(VueRouter);

const routes = [
    {
        path:"/",//默认访问路劲
        component:index //组件访问名称
    }, 
    {
        path:'/index',//访问路路径
        component:index //z组件名称
    },
    {
        path:'/shopCart', 
        component:shopCart 
    },
    {
        path:'/AboutUs', 
        component:AboutUs 
    },
    {
        path:'/Tourism', 
        component:Tourism 
    },
    {
        path:'/indexdetails/:aid', 
        component:indexdetails 
    },
    {
        path:'/otherdetails/:aid', 
        component:otherdetails 
    },
    {
        path:'/otherMeans', 
        component:otherMeans 
    },
    {
        path:'/commodity_index', 
        component:commodity_index 
    },
    {
        path:'/commodity_details', 
        component:commodity_details 
    },
    {
        path:'/commodity_order', 
        component:commodity_order 
    },
    {
        path:'/Download', 
        component:Download 
    },
    {
		path: '/login',
		component: login //登录
	},
	{
		path: '/my_account',
		component: my_account //我的账户
	},
	{
		path: '/my_order',
		component: my_order //我的订单
	},
	{
		path: '/my_set',
		component: my_set 
    },
    {
		path: '/issueOpus',
		component: issueOpus 
    },
    {
       path: '/follow',
       component: follow 
    },
    {
       path: '/commodity_foodpage',
       component: commodity_foodpage 
    },
    {
       path: '/commodity_payment',
       component: commodity_payment 
    },
    {
       path: '/commodity_accomplish',
       component: commodity_accomplish 
    }
    
   
]
var router = new VueRouter({
    routes
})

export default router;