<template>
    <div id="commodity_payment">
        <el-container>
            <el-header>
                <headerNav />
            </el-header>
            <el-main>
                <el-steps :active="1" simple finish-status="success">
                    <el-step title="确定商品" icon="el-icon-edit"></el-step>
                    <el-step title="付款" icon="el-icon-upload"></el-step>
                    <el-step title="收获确认" icon="el-icon-picture"></el-step>
                    <el-step title="评价" icon="el-icon-picture"></el-step>
                </el-steps>
                <div class="payment_method">
                    <div class="payod">
                        <el-row type="flex" justify="space-between">
                            <el-col :span="15">
                                <div><i class="el-icon-circle-check"></i></div>
                                <p>订单提交成功,还差一步就抢到心爱的商品,快快支付吧~</p>
                            </el-col>
                            <el-col :span="6">
                                <span>待支付：</span>
                                <span>¥215.00</span>
                            </el-col>
                        </el-row>
                        <!-- <el-divider></el-divider> -->
                    </div>
                    <div class="PayPal">
                        <el-tabs type="border-card" v-model="activeName">
                            <el-tab-pane label="添加银行卡支付" name="Add">

                                <el-row type="flex" justify="space-between">
                                    <el-col :span="12">
                                        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px"
                                            class="demo-ruleForm">
                                            <el-form-item label="姓名" prop="name">
                                                <el-input v-model="ruleForm.name" placeholder="请输入姓名"></el-input>
                                            </el-form-item>
                                            <el-form-item label="身份证" prop="identity">
                                                <el-input v-model="ruleForm.identity" placeholder="请输入身份证"></el-input>
                                            </el-form-item>
                                            <el-form-item label="卡号" prop="card">
                                                <el-input v-model="ruleForm.card" placeholder="请输入卡号"></el-input>
                                            </el-form-item>
                                            <el-form-item label="手机号" prop="phone">
                                                <el-input v-model="ruleForm.phone" placeholder="请输入手机号"></el-input>
                                            </el-form-item>
                                            <el-form-item label="验证码" prop="yzm">
                                                <div id="yzm">
                                                    <el-input v-model="ruleForm.yzm" placeholder="验证码">
                                                    </el-input>
                                                    <el-button type="primary">获取验证码</el-button>
                                                </div>
                                            </el-form-item>
                                            <el-form-item>
                                                <el-button type="primary" @click="submitForm('ruleForm')">立即添加</el-button>
                                                <el-button @click="resetForm('ruleForm')">重置</el-button>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                    <el-col :span="8">
                                        <div class="insurance">
                                            放心支付，全额赔付
                                        </div>
                                        <div class="company_name">
                                            <p>美惑保险</p>
                                            <p>American insurance</p>
                                        </div>
                                        <p class="commitment">
                                            <ul>
                                                <li>美惑保险承诺保, 盗用按约赔付</li>
                                                <li>支付信息强加密, 保证资金安全</li>
                                                <li>银行卡安全规范, 只用国际标准</li>
                                                <li>手机验证码验证, 安全更有保障</li>

                                            </ul>
                                        </p>
                                    </el-col>
                                </el-row>

                            </el-tab-pane>
                            <el-tab-pane label="银行卡支付" name="first">
                                <el-row>
                                    <div @click="choice(k)" v-for="(bank,k) in bank_card" :key="k">

                                        <el-col :span="5" v-bind:class="{zfbk: bank.zfbk}">

                                            <img :src="bank.logo" />
                                            <p> {{bank.text}}</p>

                                        </el-col>

                                    </div>

                                </el-row>

                            </el-tab-pane>
                            <el-tab-pane label="微信等支付">
                                <el-row>
                                    <div @click="terrace(i)" v-for="(pay,i) in paymentList" :key="i">

                                        <el-col :span="5" v-bind:class="{zfbk: pay.zfbk}">
                                            <img :src="pay.logo" />
                                            <p> {{pay.text}}</p>
                                        </el-col>

                                    </div>

                                </el-row>

                            </el-tab-pane>

                            <el-row v-if="activeName!='Add'">
                                <el-divider></el-divider>
                                <router-link to="/commodity_accomplish">
                                    <el-button type="danger" :disabled="disabled">立即支付</el-button>
                                </router-link>
                                <span v-if="hint">请选择支付方式</span>
                            </el-row>
                        </el-tabs>
                    </div>


                </div>
            </el-main>
        </el-container>

    </div>
</template>

<script>
    import headerNav from "../../components/headerNav.vue";
    import {
        container,
        header,
        main,
        row,
        col,
        icon,
        divider,
        tabs,
        TabPane,
        button,
        form,
        FormItem,
        input,
        steps,
        step
    } from 'element-ui';
    export default {
        data() {
            return {
                disabled: true,
                activeName: 'Add',
                /* 支付提示 */
                hint: true,
                bank_card: [{
                        zfbk: false,
                        logo: require('../../assets/images/logo.png'),
                        text: '中国美惑银行',
                    },

                    {
                        zfbk: false,
                        logo: require('../../assets/images/commodityCenter/zgyh.jpg'),
                        text: '中国银行 ',
                    },
                    {
                        zfbk: false,
                        logo: require('../../assets/images/commodityCenter/youzheng.jpg'),
                        text: '中国邮政银行',
                    },
                    {
                        zfbk: false,
                        logo: require('../../assets/images/commodityCenter/zg.jpg'),
                        text: '中国工商银行',
                    },
                    {
                        zfbk: false,
                        logo: require('../../assets/images/commodityCenter/zn.jpg'),
                        text: '中国农业银行',
                    },
                ],
                paymentList: [{
                        zfbk: false,
                        logo: require('../../assets/images/logo.png'),
                        text: '美惑支付',
                    },
                    {
                        zfbk: false,
                        logo: require('../../assets/images/commodityCenter/weixin.jpg'),
                        text: '微信支付',
                    },
                    {
                        zfbk: false,
                        logo: require('../../assets/images/commodityCenter/zhifubao.jpg'),
                        text: '支付宝支付',
                    },
                ],
                ruleForm: {
                    /* 姓名 */
                    name: '',
                    /* 身份证 */
                    identity: '',
                    /* 卡号 */
                    card: '',
                    /* 手机号码 */
                    phone: '',
                    /* 验证码 */
                    yzm:'',
                },
                rules: {
                    name: [{
                            required: true,
                            message: '请输入姓名',
                            trigger: 'blur'
                        },
                        {
                            min: 3,
                            max: 5,
                            message: '长度在 3 到 5 个字符',
                            trigger: 'blur'
                        }
                    ],
                    identity: [{
                            required: true,
                            message: '请输入身份证',
                            trigger: 'blur'
                        },
                        {
                            min: 18,
                            max: 18,
                            message: '请输入正确身份证',
                            trigger: 'blur'
                        }
                    ],
                    card: [{
                            required: true,
                            message: '请输入卡号',
                            trigger: 'blur'
                        },
                        {
                            min: 15,
                            max: 19,
                            message: '请输入正确卡号',
                            trigger: 'blur'
                        },
                        /* {
                            type: 'number',
                            message: '卡号暂时只支持数字',
                            trigger: 'blur',
                        } */
                    ],
                    phone: [{
                            required: true,
                            message: '请输入手机号',
                            trigger: 'blur'
                        },
                        {
                            min: 11,
                            max: 11,
                            message: '请输入正确手机号',
                            trigger: 'blur'
                        },
                        /* {
                            type: 'number',
                            message: '手机号暂时只支持数字',
                            trigger: 'blur',
                        } */
                    ],
                    yzm: [{
                            required: true,
                            message: '请输入验证码',
                            trigger: 'blur'
                        },
                       
                    ],
                }
            };
        },
        components: {
            headerNav,
            [container.name]: container,
            [header.name]: header,
            [main.name]: main,
            [row.name]: row,
            [col.name]: col,
            [icon.name]: icon,
            [divider.name]: divider,
            [tabs.name]: tabs,
            [TabPane.name]: TabPane,
            [button.name]: button,
            [form.name]: form,
            [FormItem.name]: FormItem,
            [input.name]: input,
            [steps.name]: steps,
            [step.name]: step,
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('添加成功');
                        this.activeName = 'first'
                    } else {
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },

            choice(k) {
                this.disabled = false /* 支付按钮 */
                this.hint = false /* 关闭支付提示 */
                /* 选卡支付 */
                for (let i = 0; i < this.bank_card.length; i++) {
                    if (k == i) {
                        this.bank_card[k].zfbk = true;
                    } else {
                        this.bank_card[i].zfbk = false;
                        /* 平台 */
                        for (let j = 0; j < this.paymentList.length; j++) {
                            this.paymentList[j].zfbk = false;
                        }
                    }
                }
            },
            terrace(k) {
                this.disabled = false /* 支付按钮 */
                this.hint = false /* 关闭支付提示 */
                for (var i = 0; i < this.paymentList.length; i++) {
                    if (k == i) {
                        this.paymentList[k].zfbk = true;
                    } else {
                        this.paymentList[i].zfbk = false;
                        for (let j = 0; j < this.bank_card.length; j++) {
                            this.bank_card[j].zfbk = false;
                        }
                    }
                }

            },
        }
    }
</script>

<style scoped>
    #commodity_payment .el-main {
        width: 70%;
        margin: 0 auto;
    }

    #commodity_payment>>>.el-icon-check:before {
        font-size: 24px;
        color: #57BEC3;
    }

    #commodity_payment>>>.el-step__title.is-success {
        color: #57BEC3;
        font-weight: 700;
    }

    #commodity_payment>>>.el-icon-upload:before {
        font-size: 24px;
    }

    #commodity_payment>>>.el-step__title.is-process {
        color: #57BEC3;
    }

    #commodity_payment>>>.el-icon-upload:before {
        color: #57BEC3;
    }

    #commodity_payment .payment_method {
        margin-top: 20px;

        width: 100%;

    }

    #commodity_payment .payod {
        padding-top: 10px;

    }

    #commodity_payment .payod .el-col-15 {
        display: flex;
    }

    #commodity_payment #yzm {
        display: flex;
    }

    #commodity_payment #yzm >>>.el-button--primary {
        margin-left: 20px;
    }

    #commodity_payment .payod .el-col-15 p {
        font-size: 18px;
        padding: 5px 0 0 5px;
    }

    #commodity_payment .payod .el-icon-circle-check:before {
        color: #57BEC3;
        font-size: 35px;

    }

    #commodity_payment .payod .el-col-6 {
        text-align: right;
        padding: 5px 10px 0 0;
    }

    #commodity_payment .payod .el-col-6 span:nth-child(2) {
        color: #FF0000;
        font-size: 18px;
    }

    #commodity_payment .payod .el-divider--horizontal {
        background-color: #57BEC3;
        height: 2px;
        margin: 3px 0;
    }

    #commodity_payment .el-col-8 {
        text-align: center;
        border: 1px solid #D3DCE6;
    }

    #commodity_payment .el-col-8 .insurance {

        font-size: 20px;
        color: white;
        background-color: #68aaef;
        height: 70px;
        line-height: 70px;
    }

    #commodity_payment .el-col-8 .company_name {
        font-size: 18px;
        font-weight: 600;
        color: #6080ca;
    }

    #commodity_payment .commitment ul li {
        list-style-type: disc;
        list-style-position: inside;
        line-height: 45px;
    }

    #commodity_payment .PayPal .el-col-5 {
        border: 1px solid #57BEC3;
        margin: 10px 20px;
        height: 45px;
        display: flex;
        line-height: 45px;
        cursor: pointer;
    }

    #commodity_payment .PayPal .zfbk {
        border: 1px solid red;
    }

    #commodity_payment .PayPal .el-col-5 img {
        height: 25px;
        width: 25px;
        margin: 10px 10px;
    }

    #commodity_payment .PayPal span {
        color: red;
    }

    #commodity_payment .el-button--danger {

        width: 200px;
        margin: 10px 20px;
    }
</style>
