<template>
    <div id="issueOpus">
        <el-container>
            <el-header>
                <el-row>
                    <el-col :span="20" style="padding-left:60px">
                        <div class="grid-content bg-purple">
                            <p class="logo">
                                <a href="">  
                                    美惑   
                                </a>
                            </p>
                        </div>
                    </el-col>
                    <el-col :span="4" style="text-align: end;">
                        <div class="grid-content bg-purple-light" style="padding-right:70px;">  
                            <!-- <el-avatar :size="30" :src="circleUrl"></el-avatar>                                                                                  -->
                            <el-avatar :src="personalImg" :size="30"></el-avatar>      
                            <!-- <span class="usre"> 一碗吃饱了～🌸</span> -->
                            <span class="usre">|</span>
                            <el-button type="text" class="usre" @click="open" style="color:black">退出</el-button> 
                            
                                                                                                        
                        </div>
                    </el-col>                    
                </el-row>   
                     
            </el-header>
            <el-main>
                <div class="main-content">
                    <el-row class="Publishing-title">
                        <el-col :span="24">
                            <div class="grid-content bg-purple-dark">
                                发布说说
                            </div>
                        </el-col>
                    </el-row>
                    <div class="Publishing-Works">
                        <!-- 左边 -->
                        <div class="Publishing-left ">
                            <el-row>
                                    <el-col :span="24">
                                        <div class="grid-content bg-purple-dark">
                                            <h3 class="talkcontent">添加图片</h3>
                                        </div>
                                    </el-col>
                                </el-row>
                            <div class="box">
                                <div style="margin:8px">          
                                    <el-upload
                                        action="#"
                                        list-type="picture-card"
                                        :auto-upload="false"
                                    >
                                        <i slot="default" class="el-icon-plus"></i>
                                        <div slot="file" slot-scope="{file}">
                                        <img
                                            class="el-upload-list__item-thumbnail"
                                            :src="file.url" alt=""
                                        >
                                        <span class="el-upload-list__item-actions">
                                            <span
                                            class="el-upload-list__item-preview"
                                            @click="handlePictureCardPreview(file)"
                                            >
                                            <i class="el-icon-zoom-in"></i>
                                            </span>
                                            <span
                                            v-if="!disabled"
                                            class="el-upload-list__item-delete"
                                            @click="handleDownload(file)"
                                            >
                                            <i class="el-icon-download"></i>
                                            </span>
                                            <span
                                            v-if="!disabled"
                                            class="el-upload-list__item-delete"
                                            @click="handleRemove(file)"
                                            >
                                            <i class="el-icon-delete"></i>
                                            </span>
                                        </span>
                                        </div>
                                    </el-upload>
                                    <el-dialog :visible.sync="dialogVisible">
                                         <img width="100%" :src="dialogImageUrl" alt="">
                                    </el-dialog>
                                </div>                              
                            </div>                           
                        </div>
                        <!-- 中间 -->
                        <div class="Publishing-center">
                        </div>
                        <!-- 右边 -->
                        <div class="Publishing-right">
                            <div class="right-box">
                                <el-row>
                                    <el-col :span="24">
                                        <div class="grid-content bg-purple-dark">
                                            <h3 class="talkcontent">说说内容</h3>
                                        </div>
                                    </el-col>
                                </el-row>
                                <el-input
                                    type="textarea"
                                    
                                    placeholder="填写说说的标题，最多20个字"
                                    maxlength="20"
                                    v-model="textarea1">
                                </el-input>
                                <div style="margin: 20px 0;"></div>          
                                <textarea  type="textarea" v-model="textarea2"  name="content" placeholder="填写说说的内容，最多1000个字" style="width:408px;height:200px;" class="talk-content"></textarea>
                               
                                <div style="margin: 20px 0;"></div>
                                <el-row class="addLink">
                                    <el-col :span="24">
                                        <div class="grid-content bg-purple-dark" >  
                                            <el-button type="primary" icon="el-icon-plus" round v-if="islink==false" @click="addConnect">添加链接（0/1）</el-button>
                                            <el-input
                                                placeholder="请添加链接"
                                                v-model="input"
                                                v-if="islink==true"
                                                @click="addConnect" 
                                                :autofocus="true"
                                                 @blur="away"                                                                                                                                                                                 
                                            >                               
                                                <i slot="prefix" class="myicon myicon-jing" style="font-size:26px"></i>
                                            </el-input>                          
                                        </div>                          
                                    </el-col>
                                </el-row>
                                <el-row class="release">
                                    <el-col :span="24">
                                        <div class="grid-content bg-purple-dark">
                                            <!-- <router-link to="/Tourism"> -->
                                                <el-button type="primary" round  @click="publishBut">发布</el-button>
                                            <!-- </router-link> -->
                                        </div>
                                    </el-col>
                                </el-row>
                            </div>
                        </div>
                    </div>                                                      
                </div>    
            </el-main>
            <!-- <el-footer></el-footer> -->
        </el-container>
        



    </div>
</template>
<script>
import { 
    Container, 
    Header ,
    Footer,
    Main,
    Upload,
    Row,
    Col,
    Avatar,
    Dialog,
    Input,
    Button,
    MessageBox,
    Message
 
   
} from 'element-ui';

export default {
    data () {
      return {
        personalImg: require('../../assets/images/about/1.jpg'),
        dialogImageUrl: '',
        dialogVisible: false,
        disabled: false,
        textarea1: '',
        textarea2: '',
        input:'',
        islink:'',
        centerDialogVisible: false,
         
        
       
      }
    },
   components:{     
        [Container.name]:Container,
        [Header.name]:Header,
        [Main.name]:Main,
        [Footer.name]:Footer,
        [Upload.name]:Upload,
        [Row.name]:Row,
        [Col.name]:Col,
        [Avatar.name]:Avatar,
        [Dialog.name]:Dialog,
        [Input.name]:Input,
        [Button.name]:Button,
        [MessageBox.name]:MessageBox,
        [Message.name]:Message,
      
    }, 
    methods: {
        // 图片上传
        handleRemove(file) {
            console.log(file);
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        handleDownload(file) {
            console.log(file);
        },
        
        addConnect(){            
            this.islink = !this.islink;     
        },
        away() {
            // console.log('clicked away');
            this.islink = !this.islink;
         }, 
         optOut(){
             this.$router.push('/index')
         },
        open() {
            MessageBox.confirm('您是否退出登录?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
              Message.success({
                    type: 'success',
                    message: '退出登录成功!'
                });
            }).catch(() => {
                Message.error({
                    type: 'info',
                    message: '已取消退出登录'
                });          
            });
        },
        publishBut(){
            Message.success({
                showClose: true,
                message: '发布成功',
                type: 'success'
            });
            setTimeout(()=>{
                //需要延迟的代码 :3秒后延迟跳转到首页，可以加提示什么的
                 this.$router.push('/follow')
                //延迟时间：3秒
            },2000)
        
        }
             
    },
    beforeDestroy() {
      clearTimeout(this.timer);
    }
    
}
</script>
<style scoped>
/* 头部 */
#issueOpus .grid-content .logo a{  
  width: 60px;
  height: 50px;
  background: url("../../assets/images/logo.png");
  background-size: 50px 50px;
  background-repeat: no-repeat;
  display: inline-block; 
  vertical-align: middle; 
  text-indent: -999px;
}
#issueOpus .grid-content *{
    vertical-align: middle;
}
#issueOpus .usre{
    padding-left: 6px;
}
#issueOpus .Publishing-title{
    font-size: 35px;
    font-weight: 400;
}
.main-content{
  width: 1000px;
  margin: 0 auto;
}
/* 左边 */
#issueOpus .Publishing-Works{
    display: flex;
}
/* 左边 */
#issueOpus .Publishing-left{
  width: 317px;
  height: 692px;

}
.box{
    width: 330px;
  height: 564px;
    border-radius: 20px;
    background-color: #fafafa;
    border: 1px solid #e6e6e6;
    /* display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer; */
}
/* 中间 */
#issueOpus .Publishing-center{
    width: 1px;
    height: 644px;
    background-color: #ccc;
    margin: 0px 100px 0;
}
/* 右边 */
#issueOpus .Publishing-right{
    width: 430px;
    height: 650px;
    /* border: 1px solid red; */
}
.right-box .talk-content   textarea {
    display: block;
    background: #fff;
    font-size: 14px;   
    caret-color: #5b92e1;
    color: red;
}
.right-box .talk-content {
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #DCDFE6;
    resize: none;
}

/* 布局容器 */
#issueOpus  .el-header {
    color: #333;
    line-height: 60px;
  }
#issueOpus   .el-main {  
    background-color: #E9EEF3;
    color: #333;
  }
#issueOpus   body > .el-container {
    margin-bottom: 40px;
  }
  /* 布局 */
#issueOpus   .el-row {
    margin-bottom: 20px;
  
  }
#issueOpus   .el-col {
    border-radius: 4px;
  }
 #issueOpus  .grid-content {
    min-height: 26px;
  }
#issueOpus  .row-bg {
    padding: 10px 0;
    
  }


</style>
<style >
#issueOpus  .right-box .el-input__inner {
    width: 40%;
}
#issueOpus  .addLink .el-input--prefix .el-input__inner {
    border-radius: 20px;

}
#issueOpus  .addLink .el-input__prefix, .el-input__suffix {
    top: 8px;
}
/* 右边 */
/* 按钮 */
/* .addLink .el-button--primary:focus, .el-button--primary:hover {
    background: rgb(213, 215, 218);
    border-color: rgb(213, 215, 218);
    color: #FFF;
} */
.addLink .el-button--primary {
    color: #57BEC3;
    background-color: #fff;
    border-color: #57BEC3;

}


.release .el-button--primary {
    background-color: #57BEC3;
    border-color: rgb(235, 237, 240);
}
</style>