import {
  Container,Header,Footer,Main,Row,Col,Icon,Carousel,CarouselItem,Button,Divider,Card ,Slider,Backtop,Image,Avatar
} from 'element-ui'; 
export default {
    components:{
      //头部导航
      headerNav,
      // 底部
      bottom, 
      //element组件
      [Container.name]:Container,
      [Header.name]:Header,
      [Main.name]:Main,
      [Footer.name]:Footer,
      [Row.name]:Row,
      [Col.name]:Col,
      [Icon.name]:Icon,
      [Carousel.name]:Carousel,
      [CarouselItem.name]:CarouselItem,
      [Button.name]:Button,
      [Divider.name]:Divider,
      [Card.name]:Card,
      [Slider.name]:Slider,
      [Backtop.name]:Backtop,
      [Image.name]:Image,
      [Avatar.name]:Avatar,
    }, 
    data(){
      return{ 
        style: {},
        opacity: 0, 
        swiperInfo:[
            {abImg:require('../assets/images/index/a.jpg'),title:"麻辣小龙虾",discounts:"6.5折",address:"广西南宁江南区沙井大道39号"},
