        ], 
        
        indexDetail:[]
      }
    },
    mounted() {
      window.addEventListener("scroll", this.windowScroll); //监听页面滚动
      
    }, 
		methods: {
			windowScroll() {
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				this.opacity = Math.abs(Math.round(scrollTop)) / 250;
        this.style = {background: `rgba(0, 0, 0,${this.opacity})`}
        // console.log(this.opacity);
      },
      release(){
        this.$router.push('/issueOpus');
        location.reload();
      },
      purchase(){
        this.$router.push('/commodity_index');
        // location.reload();
      },     
		},
		destroyed() {
			window.removeEventListener("scroll", this.windowScroll); //销毁滚动事件
    },
    async created() {
        this.details = details;     
        for(var i in this.details){
            let lists= this.details[i];         
            // console.log(this.details[i]);
            for(var k in lists){ 
              this.indexDetail.push(lists[k]);
              
            }
        }   
    }
    
   
}
</script>
