<template>
	<div class="my_set">
		<el-container>
			<el-header>
				<headerNav />
			</el-header>
			<el-main>
				<pbLeft />
				<div class="right">
					<div class="firstModule" id="personalDataID">
						<div class="title">
							个人资料
						</div>
						<div class="form">
							<el-form ref="form" :model="form" label-width="80px">
								<el-form-item label="昵称">
									<el-input v-model="form.name"></el-input>
								</el-form-item>
								<el-form-item label="生日">
									<el-col :span="8">
										<el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
									</el-col>
								</el-form-item>
								<el-form-item label="性别">
									<el-radio-group v-model="form.resource">
										<el-radio :label="1">男</el-radio>
										<el-radio :label="0">女</el-radio>
									</el-radio-group>
								</el-form-item>
								<el-form-item label="所在地">
									<el-cascader v-model="defaultAddress" :options="defaultAptions" @change="handleChange"></el-cascader>
								</el-form-item>
								<el-form-item label="个性签名">
									<el-input type="textarea" show-word-limit maxlength="40" rows="4" v-model="form.desc"></el-input>
								</el-form-item>
								<el-form-item class="operationBut but">
									<el-button class="operationBut" type="primary" @click="onSubmit">保存</el-button>
								</el-form-item>
							</el-form>
						</div>
					</div>
					<div class="secondModule" id="receGoodsID">
						<div class="thead">
							<div class="addTitle">
								<span>收货地址</span><span class="num" style="vertical-align: bottom;">您已经创建 <span>{{addressNum}}</span>
									个收货地址，最多可创建 <span>10</span> 个</span>
							</div>
							<div class="operationBut">
								<el-button class="operationBut" type="primary" @click="onAddress(1)" icon="el-icon-plus">新增</el-button>
							</div>
						</div>
						<div class="addressList">
							<el-row :gutter="20">
								<el-col :span="12" v-for="(val,k) in addresslist" :key="k">
									<div class="addressInfo" :class="{addressInfoBor:!val.showBut}">
										<div class="but">
											<span style="font-size: 18px;"><i class="el-icon-user-solid"></i>{{val.name}}</span>
											<el-button type="primary" size="small " v-if="val.showBut" class="defaultBut">默认地址</el-button>
											<el-button type="primary" size="small " v-if="!val.showBut" class="setdefaultBut" @click="defBut(k)">设为默认</el-button>
										</div>
										<div class="addRe">
											<i class="el-icon-s-home"></i>{{val.province+val.city+val.area+' '+val.detAddress}}
										</div>
										<div class="but">
											<div><i class="el-icon-phone"></i>{{val.phone}}</div>
											<div>
												<el-button type="primary" size="mini" @click="onAddress(0,k)">修改</el-button>
												<el-button type="primary" @click="deleAddress(k)" size="mini" slot="reference" v-if="!val.showBut">
													删除
												</el-button>
											</div>
										</div>
									</div>
								</el-col>
							</el-row>
						</div>
						<!-- 新增收货地址 -->
						<div class="addaddRess">
							<el-dialog :title="title" :lock-scroll="false" :close-on-click-modal="false" :visible.sync="dialogFormVisible">
								<el-form :model="addUpdaAddress" style="padding-bottom: 0;">
									<el-form-item label="收货人" :label-width="formLabelWidth">
										<el-input placeholder="收货人姓名" v-model="addUpdaAddress.addUpdaName" autocomplete="off"></el-input>
									</el-form-item>
									<el-form-item label="手机号" :label-width="formLabelWidth">
										<el-input placeholder="手机号" v-model="addUpdaAddress.addUpdaPhone" autocomplete="off"></el-input>
									</el-form-item>
									<el-form-item label="所在地区" :label-width="formLabelWidth">
										<el-cascader v-model="address" :options="options"></el-cascader>
									</el-form-item>
									<el-form-item label="详细地址" :label-width="formLabelWidth">
										<el-input placeholder="详细地址" v-model="addUpdaAddress.addUpdaDetAddress" autocomplete="off"></el-input>
									</el-form-item>
									<el-form-item :label-width="formLabelWidth" v-if="showUpdateOn">
										<el-checkbox v-model="setDefault">设为默认</el-checkbox>
									</el-form-item>
								</el-form>
								<div slot="footer" class="dialog-footer">
									<el-button @click="dialogFormVisible = false">取 消</el-button>
									<el-button type="primary" @click="addUpdaConfirm">确 定</el-button>
								</div>
							</el-dialog>
						</div>
					</div>
					<!-- 密码设置 -->
					<div class="thirdModule" id="pwdID">
						<div class="title">
							修改密码
						</div>
						<div class="resetInfo">
							<el-form :model="updatePwdRuleForm" status-icon :rules="updatePwdRules" ref="updatePwdRuleForm" label-width="100px"
							class="demo-ruleForm">
								<el-form-item label="原始密码" prop="primaryPass">
									<el-input type="password" placeholder="请输入原始密码" v-model="updatePwdRuleForm.primaryPass" autocomplete="off"></el-input>
								</el-form-item>
								<el-form-item label="新密码" prop="newPass">
									<el-input type="password" placeholder="请输入新密码" v-model="updatePwdRuleForm.newPass" autocomplete="off"></el-input>
								</el-form-item>
								<el-form-item label="确认密码" prop="checkPass">
									<el-input type="password" placeholder="请再次确认新密码" v-model="updatePwdRuleForm.checkPass" autocomplete="off"></el-input>
								</el-form-item>
								<el-form-item class="operationBut">
									<el-button type="primary" @click="submitForm('updatePwdRuleForm')">修改</el-button>
								</el-form-item>
							</el-form>
						</div>
					</div>
				</div>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	import areaInfo from '../utils/area.js';
	import headerNav from "../components/headerNav.vue";
	import pbLeft from "../components/pbLeft.vue";
	import {
		Header,
		Button,
		Input,
		Main,
		Container,
		// 表单
		Form,
		FormItem,
		Select,
		Option,
		Col,
		Row,
		DatePicker,
		TimePicker,
		Switch,
		CheckboxGroup,
		Checkbox,
		RadioGroup,
		Radio,
		// 级联选择器
		Cascader,
		// 弹出层
		Popconfirm,
		// 对话框
		Dialog,
		Message,
		MessageBox
	} from 'element-ui';
	export default {
		// created(){
		// 	console.log(areaInfo);
		// },
		// ngAfterViewInit(){
		// 	document.getElementById('pwdID').scrollIntoView();
		// },
		components: {
			headerNav,
			pbLeft,
			[Header.name]: Header,
			[Button.name]: Button,
			[Input.name]: Input,
			[Main.name]: Main,
			[Container.name]: Container,
			// 表单
			[Form.name]: Form,
			[FormItem.name]: FormItem,
			[Select.name]: Select,
			[Option.name]: Option,
			[Col.name]: Col,
			[Row.name]: Row,
			[DatePicker.name]: DatePicker,
			[TimePicker.name]: TimePicker,
			[Switch.name]: Switch,
			[CheckboxGroup.name]: CheckboxGroup,
			[Checkbox.name]: Checkbox,
			[RadioGroup.name]: RadioGroup,
			[Radio.name]: Radio,
			// 级联选择器
			[Cascader.name]: Cascader,
			// 弹出成
			[Popconfirm.name]: Popconfirm,
			// 对话框
			[Dialog.name]: Dialog,
			[Message.name]: Message,
			[MessageBox.name]: MessageBox,
 
		},
		mounted() {
			// 收货地址数
			this.addressNum = this.addresslist.length;
		},
		data() {
			// 修改密码
			var validatePass = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('请输入原始密码'));
				} else {
					callback();
				}
			};
			var validatePass1 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('请输入密码'));
				} else {
					if (this.updatePwdRuleForm.checkPass !== '') {
						this.$refs.updatePwdRuleForm.validateField('checkPass');
					}
					callback();
				}
			};
			var validatePass2 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('请再次输入密码'));
				} else if (value !== this.updatePwdRuleForm.newPass) {
					callback(new Error('两次输入密码不一致!'));
				} else {
					callback();
				}
			};
			return {
				// 标题
				title: '新增收货地址',
				// 设为默认
				setDefault: false,
				// 验证码 密码 确认密码
				verCode: '',
				pwd: '',
				confirmPwd: '',
				primaryPwd: '',
				// 收货地址数
				addressNum: '',
				// 新增、修改收货地址
				dialogFormVisible: false,
				addUpdaAddress: {
					addUpdaName: '',
					addUpdaPhone: '',
					addUpdaDetAddress: '',
				},
				formLabelWidth: '120px',
				// 个人资料
				form: {
					name: '云淡风清',
					date1: '2018-12-08',
					type: [],
					resource: 1,
					desc: '',
				},
				defaultAddress: ["广西壮族自治区", "南宁市", "西乡塘区"],
				defaultAptions: areaInfo,
				address: [],
				options: areaInfo,
				// 地址列表
				addresslist: [{
						name: '小西',
						showBut: true,
						province: '广西壮族自治区',
						city: '南宁市',
						area: '西乡塘区',
						detAddress: '丰达路宏桂217青年社区',
						phone: '18947544547'
					},
					{
						name: '小东',
						showBut: false,
						province: '广东省',
						city: '佛山市',
						area: '顺德区',
						detAddress: '乐从镇 88号',
						phone: '15685424514'
					},
					{
						name: '小湖',
						showBut: false,
						province: '湖南省',
						city: '长沙市',
						area: '芙蓉区',
						detAddress: '芙蓉区民政局',
						phone: '17516447544'
					},
					{
						name: '小云',
						showBut: false,
						province: '云南省',
						city: '昆明市',
						area: '五华区',
						detAddress: '五华区民政局',
						phone: '15987544551'
					},

				],
				// addUpdateN: '',
				// addUpdateK: ''
				// 新增修改中设为默认
				showUpdateOn: true,
				// 修改密码
				updatePwdRuleForm: {
					primaryPass: '',
					newPass: '',
					checkPass: '',
				},
				updatePwdRules: {
					primaryPass: [{
						validator: validatePass,
						trigger: 'blur'
					}],
					newPass: [{
						newPass: [{
							validator: validatePass1,
							trigger: 'blur'
						}],
						validator: validatePass1,
						trigger: 'blur'
					}],
					checkPass: [{
						validator: validatePass2,
						trigger: 'blur'
					}],
				}
			};
		},
		methods: {
			onSubmit() {
				// console.log('submit!');
				Message.success('修改成功');
			},
			handleChange(value) {
				console.log(value);
			},
			// 默认按钮点击
			defBut(k) {
				for (let i = 0; i < this.addresslist.length; i++) {
					if (k == i) {
						this.addresslist[k].showBut = true;
					} else {
						this.addresslist[i].showBut = false;
					}
				}
			},
			// 删除地址
			deleAddress(k) {
				MessageBox.confirm('您确定要删除该收货地址吗？', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning',
					center: true,
					lockScroll: false,
					closeOnClickModal: false,
				}).then(() => {
					this.addresslist.splice(k, 1);
					this.addressNum = this.addresslist.length;
					Message.success('删除成功!');
				}).catch(() => {
					
				});
			},
			// 修改 新增
			onAddress(n, k) {
				if (this.addresslist.length >= 10) {
					Message.error('最多可创建 10 个收货地址');
					return;
				}
				// 显示新增模块
				this.dialogFormVisible = true;
				// 新增修改判断的n
				this.addUpdateN = n;
				// 新增修改判断的k
				this.addUpdateK = k;

				// 点击修改带数据过来
				if (n == 0) {
					// 标题
					this.title = "修改收货地址";
					this.address = [];
					this.addUpdaAddress.addUpdaName = this.addresslist[k].name;
					this.addUpdaAddress.addUpdaPhone = this.addresslist[k].phone;
					this.address[0] = this.addresslist[k].province;
					this.address[1] = this.addresslist[k].city;
					this.address[2] = this.addresslist[k].area;
					this.addUpdaAddress.addUpdaDetAddress = this.addresslist[k].detAddress;
					// 判断设为默认是否显示
					if (this.addresslist[k].showBut) {
						this.showUpdateOn = false;
					} else {
						this.showUpdateOn = true;
						this.setDefault = this.addresslist[k].showBut;
					}
				}
				if (n == 1) {
					// 标题
					this.title = "新增收货地址";
					this.address = [];
					this.addUpdaAddress = {};
					this.setDefault = false;
				}
			},
			// 修改 新增确认
			addUpdaConfirm() {
				if (this.addUpdaAddress.addUpdaName == undefined || this.addUpdaAddress.addUpdaName == "") {
					Message.error('请填写收货人姓名');
				} else if (this.addUpdaAddress.addUpdaPhone == undefined || this.addUpdaAddress.addUpdaPhone == "") {
					Message.error('请填写收货人手机号');
				} else if (this.address.length == 0) {
					Message.error('请选择收货地址');
				} else if (this.addUpdaAddress.addUpdaDetAddress == undefined || this.addUpdaAddress.addUpdaDetAddress == "") {
					Message.error('请填写详细地址');
				} else {
					this.dialogFormVisible = false;
					// 新增
					if (this.addUpdateN == 1) {
						// 设为默认地址
						if (this.setDefault) {
							for (let i = 0; i < this.addresslist.length; i++) {
								this.addresslist[i].showBut = false;
							}
						}
						// 添加地址
						this.addresslist.push({
							name: this.addUpdaAddress.addUpdaName,
							phone: this.addUpdaAddress.addUpdaPhone,
							province: this.address[0],
							city: this.address[1],
							area: this.address[2],
							detAddress: this.addUpdaAddress.addUpdaDetAddress,
							showBut: this.setDefault
						});
						// 更新收货地址数
						this.addressNum = this.addresslist.length;
					}
					// 修改
					if (this.addUpdateN == 0) {
						// 设为默认地址
						if (this.setDefault) {
							for (let i = 0; i < this.addresslist.length; i++) {
								this.addresslist[i].showBut = false;
							}
						}
						// 修改地址
						this.addresslist[this.addUpdateK].name = this.addUpdaAddress.addUpdaName;
						this.addresslist[this.addUpdateK].phone = this.addUpdaAddress.addUpdaPhone;
						this.addresslist[this.addUpdateK].province = this.address[0];
						this.addresslist[this.addUpdateK].city = this.address[1];
						this.addresslist[this.addUpdateK].area = this.address[2];
						this.addresslist[this.addUpdateK].detAddress = this.addUpdaAddress.addUpdaDetAddress;
						if (!this.addresslist[this.addUpdateK].showBut) {
							this.addresslist[this.addUpdateK].showBut = this.setDefault;
						}
					}
				}

			},
			// 修改密码
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						// alert('submit!');
						Message.success('密码修改成功');
					} else {
						// console.log('error submit!!');
						return false;
					}
				});
			},

		}
	}
</script>

<style scoped>
	.my_set .el-main {
		display: flex;
		justify-content: center;
		text-align: left;
		padding-top: 50px;
		background-color: #f5f7fa;
	}


	.my_set .el-submenu .el-menu-item {
		min-width: 0;
	}

	/* 右边 */
	.el-main .right {
		width: 860px;
		/* border: 1px solid red; */
		margin-left: 20px;
	}

	/* 标题 */
	.right .title {
		margin-bottom: 30px;
		font-size: 18px;
	}


	.right>div {
		background-color: #fff;
		padding: 30px;
		margin-bottom: 50px;
	}

	/* 公共操作按钮样式 */
	.right .operationBut button {
		background-color: #f5f5f5;
		color: #666;
		border-color: #e1e1e1
	}

	.right .operationBut :hover {
		border-color: #57BEC3;
		color: #57BEC3;
		transition: 0.1s;
		border-radius: 4px;
	}

	/* 第一模块 */


	/* 保存按钮 */
	.firstModule .form .but {
		margin-bottom: 0;
		display: flex;
		justify-content: flex-end;
	}


	.firstModule>>>.el-textarea .el-input__count {
		background: none;
	}

	/* 长度统计 */
	.firstModule .form>>>.el-input__count {
		right: 25px;
	}

	/* 第二模块 */


	.thead {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-bottom: 30px;
	}

	.thead .addTitle {
		font-size: 18px;
	}

	.thead .addTitle .num {
		font-size: 15px;
		margin-left: 20px;
		vertical-align: bottom;
		color: grey;
	}

	.thead .addTitle .num span {
		color: #57BEC3;
		font-size: 18px;
	}

	/* 列 */

	.addressList .addressInfo {
		background: #fff;
		border-radius: 4px;
		margin-bottom: 30px;
		padding: 10px;
		border: 4px solid transparent;
		border-image: 5 repeating-linear-gradient(-45deg, #81cfd3 0, #57BEC3 10%, transparent 0, transparent 15%, #80b3e6 0, #5ba2ea 25%, transparent 0, transparent 30%);
	}

	.addressList .addressInfoBor {
		border-image: 5 repeating-linear-gradient(-45deg, #e9e9e9 0, #d0d0d0 10%, transparent 0, transparent 15%, #ccc 0, #ccc 25%, transparent 0, transparent 30%);
	}


	.addressList i {
		color: #ccc;
		margin-right: 5px;
	}

	.addressList .addressInfo>div {
		padding: 8px 10px;
	}

	/* 地址信息按钮 */
	.addressList .but button {
		background-color: #fafafa;
		border: 1px solid #c2c2c2;
		color: #333;
	}

	.addressList .but {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	/* 默认地址按钮 */
	.addressList .but .defaultBut {
		background-color: #57BEC3;
		border-color: #57BEC3;
		color: #fff;
		opacity: 0.7;
	}

	/* 设置默认地址按钮 */
	.addressList .but .setdefaultBut {
		background-color: #f5f5f5;
		color: #666;
		border-color: #e1e1e1;
	}

	/* 地址 */
	.addressList .addRe {
		height: 44px;
		word-break: break-all;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		overflow: hidden;
	}

	/* 新增form表单 */
	.addaddRess>>>.el-dialog__body {
		padding-bottom: 0;
	}

	/* 第三模块 */
	.thirdModule .resetInfo>.el-input {
		margin-bottom: 20px;
	}

	/* 保存按钮 */
	.thirdModule .resetInfo .operationBut {
		display: flex;
		justify-content: flex-end;
	}
</style>
<style>
	.el-popper .el-cascader-panel {
		height: 200px;
	}
</style>
