<template>
    <div id="shopCart">
        <el-container>
            <el-header><headerNav /></el-header>
            <el-main>
                <div class="shopCart_header" >
                        <div class="shopCart_search">
                            <h1 >美惑购物车</h1>
                            <el-input placeholder="搜索商品"  class="input-with-select"> 
                                <el-button slot="append" icon="el-icon-search" class="search_btn"></el-button>
                            </el-input>
                        </div>
                        
                    </div>
                <div class="container"> 
                    <div class="allGoods">
                        <span style="font-size: 16px; font-weight: 700;" class="allGoods_caption">全部商品</span>
                        <div class="requestAddress">
                            配送地址：
                            <el-select v-model="value" placeholder="广西省南宁市西乡塘区">
                                <el-option
                                v-for="item in siteList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                                <span style="float: left">{{ item.label }}</span>
                                <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
                                </el-option>
                            </el-select>
                        </div>
                    </div>
                    <div class="checkAll">
                        <el-row>
                            <el-col :span="2" style=" text-align: center;">
                                <div class="grid-content bg-purple">
                                    <el-checkbox  v-model="ischeckAll" @change="handleCheckAllChange">全选</el-checkbox>
                                </div>
                            </el-col>
                            <el-col :span="4"><div class="goods">商品</div></el-col>
                            <el-col :span="7"><div class="grid-content bg-purple"></div></el-col>
                            <el-col :span="2">单价</el-col>
                            <el-col :span="3">数量</el-col>
                            <el-col :span="3">小计</el-col>
                            <el-col :span="2">操作</el-col>
                        </el-row> 
                         
                    </div>  
                </div>  
                <div v-if="this.shopPurchases.length>0" class="container">
                        <div class="cart_body" v-for="(shopPurchases,shopIndex) in shopPurchases" :key="shopIndex">
                            <div class="storeName clearfix">
                                <el-checkbox  v-model="shopPurchases.checked" @change="handleCheckedListAllChange( shopIndex,shopPurchases.shopId,$event)" :key="shopIndex">{{shopPurchases.shopName}}</el-checkbox>
                            </div>
                            <ul class="cartList">
                                <li v-for="(list,listIndex) in shopPurchases.list" :key="listIndex">
                                    <el-col :span="1">
                                        <el-checkbox  v-model="list.checked" @change="handleCheckedListChange(shopIndex,list.id,shopPurchases.shopId,$event)" :key="listIndex"></el-checkbox>
                                    </el-col>
                                    <el-col :span="7">
                                        <div class="goods">
                                            <el-image :src="list.img" class="goods_img clearfix">
                                                <div slot="placeholder" class="image-slot">
                                                    加载中<span class="dot">...</span>
                                                </div>
                                            </el-image>
                                            <span class="goods_title">{{list.title}}</span>
                                        </div>
                                    </el-col>
                                    <el-col :span="5"><div class="grid-content bg-purple"></div></el-col>
                                    <el-col :span="2">{{list.currentprice}}</el-col>
                                    <el-col :span="3">
                                        <el-input-number v-model="list.count" @change="handleChange(list.count,list.id)" :min="1" :max="10"  size="mini" label="描述文字"></el-input-number>
                                    </el-col>
                                    <el-col :span="3">{{(list.currentprice*list.count).toFixed(2)}}</el-col>
                                    <el-col :span="2">
                                        <el-button type="primary" icon="el-icon-delete" @click="delList(list.id,shopIndex)"></el-button>
                                    </el-col>
                                </li>
                            </ul>
                        </div>  
                    </div>
                <div v-else class="cartEmpty">  
                    购物车暂时无商品
                    <router-link to="/commodity_index" style="color: rgb(19, 123, 221);">快去逛逛吧！</router-link > 
                </div>
                <!-- 提交订单 -->
                <div class="submitOrder_box container">
                    <div class="submitOrder"> 
                        <el-col :span="1"><el-checkbox v-model="ischeckAll" @change="handleCheckAllChange">全选</el-checkbox></el-col>
                        <el-col :span="3"><a title="亲，点我可快速删除选中商品哦!" @click="delChecked">删除选中商品</a></el-col>
                        <el-col :span="2"><a title="亲，点我可快速清理购物车商品哦!" @click="emptyShopCart">清理购物车</a></el-col>
                        <el-col :span="10"></el-coL>
                        <el-col :span="3"><a>已选择的商品:<em>{{toalCount}}</em>件</a></el-col>
                        <el-col :span="3">总价：<em>￥{{toalPrice}}</em></el-coL>
                        <el-col :span="2"><el-button type="primary" @click="onSubmit">去结算</el-button></el-coL> 
                    </div>
                </div>
            </el-main>
        </el-container>
    </div>
</template>
<script>   
// https://blog.csdn.net/lei_0912/article/details/99289563
import headerNav from "../components/headerNav.vue";

import {
    Container,Header,Main,Icon,Input,Button,Select,Option,Checkbox,checkboxGroup,Col, Row,Image,inputNumber,MessageBox
} from 'element-ui';  
export default {
    components:{
        //头部导航
        headerNav, 
        //element组件
        [Container.name]:Container,
        [Header.name]:Header,
        [Main.name]:Main,
        [Icon.name]:Icon,
        [Input.name]:Input,
        [Button.name]:Button,
        [Select.name]:Select,
        [Option.name]:Option,
        [Checkbox.name]:Checkbox,
        [checkboxGroup.name]:checkboxGroup,
        [Col.name]:Col,
        [Row.name]:Row,
        [Image.name]:Image,
        [inputNumber.name]:inputNumber,
        [MessageBox.name]:MessageBox
    },
    data(){
        return{ 
            siteList: [
                {label: '广西省南宁市西乡塘区'}, 
                {label: '广西省南宁市西乡塘区'},
                {label: '广西省南宁市西乡塘区'},  
            ],
            toalCount:0,//总件数
            toalPrice:0.00 ,//总价 
            ischeckAll:false,//全选
            carList:[], 
            shopPurchases:[],
            list:[],
            shopIdArr:[],
            value: '', 
            src:require ('../assets/images/shopCart/shopCart_bg1.jpg'), 
            num: 1 
            }
    }, 
    created() {   
        // https://www.jianshu.com/p/6f9035f64554
        //  localStorage.clear()
        // 从vuex中拿出数据，然后进行组装
        this.carList = this.$store.state.car; 
        let shopPurchases = [];
            this.carList.map(carItem => {
        if (shopPurchases.length == 0) {
            shopPurchases.push({ 
                shopId: carItem.shopId,
                shopName: carItem.shopName, 
                shopIcon: carItem.shopIcon, 
                checked: carItem.checked, 
                list: [carItem] 
            })
        } else {
            let res = shopPurchases.some(item=> {//判断相同shopId，有就添加到当前项
            if (item.shopId == carItem.shopId) {
                item.list.push(carItem)
                return true
            }
            })
            if (!res) {//如果没找相同shopId，则添加一个新对象
                shopPurchases.push({ 
                    shopId: carItem.shopId,
                    shopName: carItem.shopName, 
                    shopIcon: carItem.shopIcon, 
                    checked: carItem.checked, 
                    list: [carItem] 
                })
            }
        }
        })
        this.shopPurchases=shopPurchases
    },
    methods: {
        // 总全选
        handleCheckAllChange(e){ //一级change事件 
            this.ischeckAll = e
            for(var i=0,len=this.shopPurchases.length; i<len; i++){ //二级全选反选
                this.shopPurchases[i].checked = e
                for(var j=0,len1=this.shopPurchases[i].list.length; j<len1; j++){//三级全选反选
                    this.shopPurchases[i].list[j].checked = e
                }
            } 
            this.getToalCount()//调用总计方法，全选关联总计
        }, 
        /* 店铺的全选 
            index是店铺的下标key
            topId是店铺的ID
        */
        handleCheckedListAllChange(index, topId, e) { //二级change事件
            this.shopPurchases[index].checked = e//二级勾选后，子级全部勾选或者取消
            var childrenArray = this.shopPurchases[index].list
            if(childrenArray)
                for(var i=0,len=childrenArray.length; i<len; i++){
                    childrenArray[i].checked = e
                }
            this.getIsCheckAll()
        },  
         /* 产品的单选
            topIndex是店铺的下标key
            sonId是产品的ID
            topId是店铺的ID
        */
        handleCheckedListChange(topIndex, sonId, topId, e){//三级change事件
            var childrenArray = this.shopPurchases[topIndex].list
            var tickCount = 0, unTickCount = 0, len = childrenArray.length
            for(var i = 0; i < len; i++){
                if(sonId == childrenArray[i].id){
                    childrenArray[i].checked = e
                } 
                if(childrenArray[i].checked == true){
                    tickCount++
                } 
                if(childrenArray[i].checked == false) {
                    unTickCount++
                }
            }
            if(tickCount == len) {//三级级全勾选
                this.shopPurchases[topIndex].checked = true 
            } else if(unTickCount == len) {//三级级全不勾选
                this.shopPurchases[topIndex].checked = false 
            } else {
                this.shopPurchases[topIndex].checked = false
            }
            // this.$store.commit("updateCarChecked",{sonId,e})
            this.getIsCheckAll()  
        },
        getIsCheckAll(){
            var tickCount = 0, unTickCount = 0,ArrLength = this.shopPurchases.length
            for(var j=0; j<ArrLength; j++){//全选checkbox状态
                if(this.shopPurchases[j].checked == true) tickCount++
                if(this.shopPurchases[j].checked == false) unTickCount++
            }
            if(tickCount == ArrLength) {//二级全勾选
                this.ischeckAll = true
            } else if(unTickCount == ArrLength) {//二级全不勾选
                this.ischeckAll = false 
            } else {
                this.ischeckAll = false
            }
            this.getToalCount()//调用总计方法，全选关联总计 
        },
        // 总数
         getToalCount(){
             let toalCount=0;
             let toalPrice=0; 
             for(let i=0;i<this.shopPurchases.length;i++){
                 let lists =this.shopPurchases[i].list;
                 lists.forEach(item=>{
                    if(item.checked){
                        toalCount=toalCount+parseInt(item.count)
                        toalPrice=toalPrice+(parseInt(item.currentprice).toFixed(2)*item.count) 
                    }
                 }) 
             } 
             this.toalCount=toalCount;
             this.toalPrice=toalPrice;
             
         },
        // count加减的累加
        handleChange(value,id) {
            this.value=value; 
            this.$store.commit("updateCarInfo",{
                id:id,
                count: this.value
            }) 
            // 调用总数
            this.getToalCount()
        },
        // 删除对应列表
        delList(Id,index){  
            for(let i=0;i<this.shopPurchases.length;i++){ 
                var lists = this.shopPurchases[i].list;   
                lists.filter(cloth=>{ 
                    let cloth_id = cloth.id   
                    if(cloth_id==Id){ 
                        let sum =  lists.splice(cloth,1);  
                        return sum
                    }   
                })   
                // 判断店铺内产品是否还有，如果没有则删掉对应的店铺 
                if(lists.length==0){ 
                    this.shopPurchases.splice(index,1)
                }   
            } 
            this.$store.commit('removelist',Id);
            this.getToalCount() 
        },
        // 删除选中的商品
        delChecked(){
            var that = this;  
            var del=confirm("确定要删除选中的商品吗？")
            // let isChecked=[],
            if (del==true){ 
                that.shopPurchases.forEach(function(value,index){
                    let list = that.shopPurchases[index].list;
                    list.forEach(function(list_vl,listIndex){
                        if(list_vl.checked){//如果选择的单品则删除选择的商品
                            list.splice(listIndex,1);   
                        }  
                    });   
                    if (value.checked) {//如果店铺全部选择则删除店铺
                        that.shopPurchases.splice(index,1);
                    
                    } 
                    if(that.ischeckAll){//如果全部选择则删除
                        that.shopPurchases.splice(that.shopPurchases,1);  
                        that.ischeckAll=false;
                    } 
                });
                var a = true; 
                this.$store.commit('removeChecked',a);
            }   
            this.getToalCount() 
        },
        // 清空购物车
        emptyShopCart(){
            var r=confirm("确定要清空所有的购物车吗？")
            if (r==true){
                this.shopPurchases=[]
            } 
            this.$store.commit('emptyShopCart');
             this.getToalCount()
        }, 
        onSubmit(){ 
            this.$router.push('/commodity_order')
        }
    },
    watch:{ 
    }
}
</script>
<style>  
#shopCart .el-main {
    /* background-color: #E9EEF3; */
    color: #333;
    text-align: center;
    line-height: 160px;
    padding: 0;  
} 
body >#shopCart .el-container {
    margin-bottom: 40px;
} 
#shopCart .input-with-select{
    background-color: #fff;
    width: 400px;
    border-radius: 10px;
}
#shopCart .shopCart_header{
    height: 400px;
    /* background-color: antiquewhite; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: url('../assets/images/shopCart/shopCart_bg1.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
#shopCart .shopCart_header .shopCart_search{
    width: 100%;
}
#shopCart .shopCart_header h1{
    color: #57BEC3;
    /* font:40px "Trebuchet MS", Arial, Helvetica, sans-serif; */
    font-size: 40px;
    ;font-family: Georgia, "Times New Roman", Times, serif;
    /* font-family: Verdana, Geneva, sans-serif; */
    /* font-family: "Trebuchet MS", Arial, Helvetica, sans-serif; */
    /* font-family:"Trebuchet MS", Arial, Helvetica, sans-serif; */  
    text-align: center;
    
}
#shopCart .shopCart_header  .search_btn{
    background-color: #57BEC3;
    border-top-left-radius: 0; 
    border-bottom-left-radius: 0;
    color: #fff;
    font-size: 16px;
}
#shopCart .allGoods{
    margin-top: 15px;
    width: 100%;
    height: 45px; 
    display: flex;
    justify-content: space-between;
    align-items: center;
}
#shopCart .allGoods .allGoods_caption{
    display: block; 
    height: 45px;
    line-height: 45px;
    border-bottom: 2px solid red;
    color: red; 
}
#shopCart .allGoods .requestAddress{
    height: 100%;
    line-height: 45px;
    font-size: 14px;
    color: #666; 
}
#shopCart .allGoods .requestAddress .el-input__inner{
    height: 30px;
    line-height: 30px;
}
#shopCart .checkAll{
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #e9e9e9;
    background-color: #f3f3f3;
}
#shopCart .checkAll .el-row { 
    height: 40px;
    width: 100%;
    line-height: 40px;
}
#shopCart .checkAll .el-row:last-child {
    margin-bottom: 0;
}  
#shopCart .checkAll .grid-content {
    min-height: 36px;
} 
 #shopCart .storeName{
    width: 100%;
    height: 30px;
    line-height: 30px;
    margin-top: 10px;
    padding-left: 16px;
    font-weight: 700;
    text-align: left;
    border-bottom: 2px solid #ccc;
    box-sizing: border-box;
    
}
#shopCart .cartList{
    width: 100%;
    /* background-color: #dff8fa;  */
    /* background-color: rgba(0, 0, 0, 0.4); */
    border: 1px solid #f1f1f1;
}
#shopCart .cartList li{
    width: 100%;
    height: 100px;
}
#shopCart .cartList .el-col{
    height: 100px;  
    display: flex;
    align-items: center;
    justify-content: center;
}
#shopCart .cartList .goods{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
#shopCart .cartList .goods_img{
    width:30%;
    height: 80px;
    vertical-align: middle;
}
#shopCart .cartList .goods_title{ 
    width: 70%;
    min-height: 35px;
    /* height: 35px;  */
    font-size: 14px; 
    display: block;  
    line-height: 18px;
    text-align: left;
    padding-left: 5px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
}
#shopCart .cartList .el-button--primary {
    background-color: #fff;
    color: red;
    font-size: 20px;
    border: 0;
}
#shopCart .submitOrder_box{
    margin-top: 20px;
    /* width: 100%; */
    height: 80px;
    position: relative;
}
#shopCart .submitOrder{
    width: 100%;
    height: 50px;
    line-height: 50px;
    padding-left: 16px;
    box-sizing: border-box;
    background-color: #e5e5e5; 
    position: absolute;
    bottom: 40px; 
    display: flex; 
}
#shopCart .submitOrder em{
    color: red;
    font-weight: 700;
    font-size: 16px;
}
#shopCart .submitOrder .el-button--primary{
    color:#fff;
    font-size: 18px;
    font-weight: 700;
    background-color:red;
    border-color: red;
}
#shopCart .submitOrder .el-button{
    border-radius: 0px;
    padding: 15px 20px;
}
/* 购物车为空 */
.cartEmpty{
    text-align: center; 
    font-size: 18px;
    font-family: Verdana, Geneva, sans-serif; 
}
 
</style>