		methods: {
			//计算每个图片的宽度或者是列数
			calculationWidth() {
				let domWidth = document.getElementById("v-waterfall").offsetWidth;
				if (!this.waterfallImgWidth && this.waterfallImgCol) {
					this.waterfallImgWidth = (domWidth - this.waterfallImgRight * this.waterfallImgCol) / this.waterfallImgCol;
				} else if (this.waterfallImgWidth && !this.waterfallImgCol) {
					this.waterfallImgCol = Math.floor(domWidth / (this.waterfallImgWidth + this.waterfallImgRight))
				}
				//初始化偏移高度数组
				this.waterfallDeviationHeight = new Array(this.waterfallImgCol);
				for (let i = 0; i < this.waterfallDeviationHeight.length; i++) {
					this.waterfallDeviationHeight[i] = 0;
				}
				this.imgPreloading();
			},
			//图片预加载
			imgPreloading() {
				for (let i = 0; i < this.imgList.length; i++) {
					let aImg = new Image();
					aImg.src = this.imgList[i];
					aImg.onload = aImg.onerror = () => {
						let imgData = {};
						imgData.height = this.waterfallImgWidth / aImg.width * aImg.height;
						imgData.src = this.imgList[i];
						imgData.myimg = this.MyList[i].myimgs;
						imgData.mytitle = this.MyList[i].myTitle;
						imgData.mytext = this.MyList[i].myText;
						imgData.myurl = this.MyList[i].myUrl;
						imgData.myname = this.MyList[i].myName;
						imgData.mynum = this.MyList[i].myNum;
						// imgData.title = '标题';// 说明文字（也可以自己写数组，或者封装json数据，都可以，但是前提是你会相关操作，这里不赘述）
						// imgData.info = '详情说明：啦啦啦啦啦';// 说明文字
						this.waterfallList.push(imgData);
						this.rankImg(imgData);
					}
				}
			},
			//瀑布流布局
            rankImg(imgData) {
                let {
                    waterfallImgWidth,
                    waterfallImgRight,
                    waterfallImgBottom,
                    waterfallDeviationHeight,
                    // waterfallImgCol
                } = this;
                let minIndex = this.filterMin();
                imgData.top = waterfallDeviationHeight[minIndex];
                imgData.left = minIndex * (waterfallImgRight + waterfallImgWidth);
                // waterfallDeviationHeight[minIndex] += imgData.height + waterfallImgBottom;// 不加文字的盒子高度
                waterfallDeviationHeight[minIndex] += imgData.height + waterfallImgBottom + 70;// 加了文字的盒子高度，留出文字的地方（这里设置56px）
                // console.log(imgData);
            },
            /**
                 * 找到最短的列并返回下标
                 * @returns {number} 下标
            */
           filterMin() {
                const min = Math.min.apply(null, this.waterfallDeviationHeight);
                return this.waterfallDeviationHeight.indexOf(min);
           }
