<script>
import headerNav from "../../components/headerNav.vue";
import bottom from "../../components/bottom.vue";
import elseDetails from '../../utils/elseDetails.js'
import { 
    Container, 
    Header ,
    Aside,
    Main,
    Footer,
    Input,
    Button,
    Tabs,
    TabPane,
    Backtop,
    Row,
    Col,
    Avatar
   
} from 'element-ui';
export default {
    data() {
        return {
            input: '',
            activeName: 'first',
            count: 0,
            visible: false,  
            WeChatimg:require('../../assets/images/about/4.jpg'),
            NewestInfo:[],//最新
            NativeInfo:[],//特产
            FoodInfo:[],//美食
            KeepsakeInfo:[],//纪念品
            TravelInfo:[],//周边游
            MinsuInfo:[],    //民宿                  
      }      
    },
    components:{
        headerNav,
        bottom,
        [Aside.name]:Aside,
        [Container.name]:Container,
        [Header.name]:Header,
        [Main.name]:Main,
        [Footer.name]:Footer,
        [Input.name]:Input,
        [Button.name]:Button,
        [Tabs.name]:Tabs,
        [TabPane.name]:TabPane,
        [Backtop.name]:Backtop,  
        [Row.name]:Row,
        [Col.name]:Col,   
        [Avatar.name]:Avatar, 
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },
      load () {
        this.count += 2
      },
    },
    async created() {
        this.elseDetails = elseDetails;     
        for(var i in this.elseDetails){
            let lists= this.elseDetails[i];         
            for(var k in lists){ 
                if(lists[k].type=="NewestInfo"){//最新
                    this.NewestInfo.push(lists[k]); 
                }
                if(lists[k].type=="NativeInfo"){//特色特产
                    this.NativeInfo.push(lists[k]); 
                }
                if(lists[k].type=="FoodInfo"){//美食
                    this.FoodInfo.push(lists[k]); 
                }
                if(lists[k].type=="KeepsakeInfo"){//纪念品
                    this.KeepsakeInfo.push(lists[k]); 
                }
                if(lists[k].type=="TravelInfo"){//周边游
                    this.TravelInfo.push(lists[k]); 
                }
                if(lists[k].type=="MinsuInfo"){//民宿
                    this.MinsuInfo.push(lists[k]); 
                }               
            }
        }   
    }
}
</script>
