<template>
	<div class="my_account">
		<el-container>
			<el-header>
				<headerNav />
			</el-header>
			<el-main>
				<pbLeft />
				<div class="right">
					<div class="balance">
						<span class="title">账户余额</span>
						<span class="money">{{allMoney}}</span>
						<el-dialog width="30%" title="填写充值金额" :close-on-click-modal="false" :lock-scroll="false" :visible.sync="dialogTableVisible">
							<el-input placeholder="请输入充值金额" maxlength="13" v-model="money">
								<template slot="prepend">充值金额:</template>
							</el-input>
							<el-dialog :close-on-click-modal="false" title="支付方式" :visible.sync="innerVisible" append-to-body>
								<div style="position: absolute;top: 23px;left: 150px; font-size: 16px;">充值金额<span style="color: red; font-size: 17px;font-weight: bold;">{{money}}</span>元</div>
								<div class="PayPal">
									<el-tabs :stretch="true" type="border-card" v-model="activeName">
										<el-tab-pane label="银行卡" name="first">
											<el-row>
												<div @click="choice(k)" v-for="(bank,k) in bank_card" :key="k">
													<el-col :span="5" v-bind:class="{zfbk: bank.zfbk}">
														<img :src="bank.logo" />
														<p> {{bank.text}}</p>
													</el-col>
												</div>
											</el-row>
										</el-tab-pane>
										<el-tab-pane label="微信等支付">
											<el-row>
												<div @click="terrace(i)" v-for="(pay,i) in paymentList" :key="i">
													<el-col :span="5" v-bind:class="{zfbk: pay.zfbk}">
														<img :src="pay.logo" />
														<p> {{pay.text}}</p>
													</el-col>
												</div>
											</el-row>
										</el-tab-pane>
										<el-row v-if="activeName!='Add'">
											<el-divider></el-divider>
											<el-button type="danger" :disabled="disabled" @click="rechargeNow">立即充值</el-button>
											<span v-if="hint">请选择充值方式</span>
										</el-row>
									</el-tabs>
								</div>
							</el-dialog>
							<div slot="footer" class="dialog-footer">
								<el-button type="primary" @click="nextStep">下一步</el-button>
							</div>
						</el-dialog>
						<el-button type="primary" class="but_r" @click="dialogTableVisible = true">充值</el-button>
						<el-button type="primary" class="but_w" @click="WithdrawalShow = true">提现</el-button>
						<el-dialog :close-on-click-modal="false" title="提现到" :visible.sync="WithdrawalShow" append-to-body>
							<div class="PayPal">
								<div style="position: absolute;top: 23px;left: 150px; font-size: 16px;">余额<span style="color: red; font-size: 17px;font-weight: bold;">{{allMoney}}</span>元</div>
								<el-row>
									<div style="margin-bottom: 20px;margin-left: 20px;">
										<el-input placeholder="请输入提现金额" maxlength="13" v-model="Withdrawal">
											<template slot="prepend">提现金额:</template>
										</el-input>
									</div>
								</el-row>
								<el-row>
									<div @click="choice(k)" v-for="(bank,k) in bank_card" :key="k">
										<el-col :span="5" v-bind:class="{zfbk: bank.zfbk}">
											<img :src="bank.logo" />
											<p> {{bank.text}}</p>
										</el-col>
									</div>
								</el-row>
								<el-row v-if="activeName!='Add'">
									<el-divider></el-divider>
									<el-button type="danger" :disabled="disabled" @click="WithdrawalNow">立即提现</el-button>
									<span v-if="hint">请选择银行卡</span>
								</el-row>
							</div>
						</el-dialog>
					</div>
					<el-tabs type="border-card" :stretch="true">
						<el-tab-pane label="充值记录">
							<el-table :data="czTable" style="width: 100%">
								<el-table-column prop="czDate" label="充值时间" width="180">
								</el-table-column>
								<el-table-column prop="czType" label="充值方式" width="180">
								</el-table-column>
								<el-table-column prop="czMoney" label="充值金额(元)">
								</el-table-column>
								<el-table-column prop="czState" label="充值状态">
								</el-table-column>
							</el-table>
						</el-tab-pane>
						<el-tab-pane label="提现记录">
							<el-table :data="txTable" style="width: 100%">
								<el-table-column prop="txDate" label="提现时间" width="180">
								</el-table-column>
								<el-table-column prop="txType" label="提现银行" width="180">
								</el-table-column>
								<el-table-column prop="txMoney" label="提现金额(元)">
								</el-table-column>
								<el-table-column prop="txState" label="提现状态">
								</el-table-column>
							</el-table>
						</el-tab-pane>
					</el-tabs>
					<div class="bankCard">
						<el-row>
							<el-col :span="3">
								<div class="title grid-content">我的银行卡</div>
							</el-col>
							<el-col :span="8">
								<div class="grid-content bankNum">
									已添加<span>{{CCnum}}</span>张信用卡，<span>{{DCnum}}</span>张储蓄卡
								</div>
							</el-col>
						</el-row>
						<el-row :gutter="20">
							<el-col :span="12" v-for="(val,k) in bankCardList" :key="k">
								<div @mouseover="mouseOver(k)" @mouseleave="mouseLeave(k)" class="grid-content bg-purple bankCardList">
									<el-row style="background-color: #fcfcfc; padding: 10px 20px;">
										<el-col :span="2">
											<div><img height="25px" width="25px" style="margin-top: 5.5px;" :src="require('../assets/images/commodityCenter/'+val.logo+'')" /></div>
										</el-col>
										<el-col :span="8">
											<div>{{val.bankName}}</div>
										</el-col>
										<el-col :span="6">
											<div>
												尾号{{val.num.substr(val.num.length-4,4)}}
											</div>
										</el-col>
										<el-col :span="5">
											<div>
												{{val.type}}
											</div>
										</el-col>
									</el-row>
									<div style="padding: 20px 25px;">
										<el-row type="flex" justify="space-between">
											<el-col :span="12">
												<div>持卡人姓名：*{{val.userName.substr(1,)}}</div>
											</el-col>
											<el-col v-show="val.operation" style="text-align: right;color: #57BEC3;cursor: pointer;" :span="4">
												<div>修改</div>
											</el-col>
										</el-row>
										<el-row type="flex" justify="space-between">
											<el-col :span="12">
												<div>手机号：{{val.phone.substr(0,3)+"****"+val.phone.substr(val.phone.length-4,4)}}</div>
											</el-col>
											<el-col v-show="val.operation" style="text-align: right;color: #57BEC3;cursor: pointer;" :span="4">
												<div @click="deleBankCard(k)">删除</div>
											</el-col>
										</el-row>
									</div>
								</div>
							</el-col>
							<el-col :span="12">
								<div id="bankCardBut" @click="bankCardAddShow = true">
									<div><i class="el-icon-plus"></i></div>
									<div>添加银行卡</div>
								</div>
							</el-col>
							<el-dialog title="添加银行卡" :close-on-click-modal="false" :visible.sync="bankCardAddShow">
								<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
									<el-form-item label="姓名" prop="name">
										<el-input v-model="ruleForm.name" placeholder="请输入姓名"></el-input>
									</el-form-item>
									<el-form-item label="身份证" prop="identity">
										<el-input v-model="ruleForm.identity" placeholder="请输入身份证"></el-input>
									</el-form-item>
									<el-form-item label="卡号" prop="card">
										<el-input v-model="ruleForm.card" placeholder="请输入卡号"></el-input>
									</el-form-item>
									<el-form-item label="手机号" prop="phone">
										<el-input v-model="ruleForm.phone" placeholder="请输入手机号"></el-input>
									</el-form-item>
									<el-form-item label="验证码" prop="yzm">
										<div id="yzm" style="display: flex;">
											<el-input v-model="ruleForm.yzm" placeholder="验证码">
											</el-input>
											<el-button type="primary">获取验证码</el-button>
										</div>
									</el-form-item>
									<el-form-item>
										<el-button type="primary" @click="submitForm('ruleForm')">立即添加</el-button>
										<el-button @click="resetForm('ruleForm')">重置</el-button>
									</el-form-item>
								</el-form>
							</el-dialog>
						</el-row>
					</div>
				</div>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	import headerNav from "../components/headerNav.vue";
	import pbLeft from "../components/pbLeft.vue";
	import {
		Header,
		Button,
		Input,
		Main,
		Container,
		// 标签
		tabs,
		TabPane,
		// 账单表格
		Table,
		TableColumn,
		Dialog,
		Row,
		Col,
		Form,
		FormItem,
		Divider,
		Message,
		MessageBox,
		Notification
	} from 'element-ui';
	export default {
		components: {
			headerNav,
			pbLeft,
			[Header.name]: Header,
			[Button.name]: Button,
			[Input.name]: Input,
			[Main.name]: Main,
			[Container.name]: Container,
			// 标签
			[tabs.name]: tabs,
			[TabPane.name]: TabPane,
			// 账单表格
			[Table.name]: Table,
			[TableColumn.name]: TableColumn,
			[Dialog.name]: Dialog,
			[Row.name]: Row,
			[Col.name]: Col,
			[Form.name]: Form,
			[FormItem.name]: FormItem,
			[Divider.name]: Divider,
			[Message.name]: Message,
			[MessageBox.name]: MessageBox,
			[Notification.name]: Notification,
		},
		data() {
			return {
				// 账单
				czTable: [{
						czDate: '2020-07-02',
						czType: '支付宝',
						czMoney: '2000',
						czState: '成功'
					}, {
						czDate: '2020-05-04',
						czType: '微信',
						czMoney: '100',
						czState: '成功'
					},
					{
						czDate: '2020-01-04',
						czType: '中国邮储储蓄银行',
						czMoney: '5000',
						czState: '成功'
					},
				],
				txTable: [{
						txDate: '2020-9-02',
						txType: '中国农业储蓄银行',
						txMoney: '2000',
						txState: '成功'
					}, {
						txDate: '2020-05-04',
						txType: '中国邮储储蓄银行',
						txMoney: '3000',
						txState: '成功'
					},
					{
						txDate: '2020-02-04',
						txType: '中国建设储蓄银行',
						txMoney: '1000',
						txState: '成功'
					},
				],
				allMoney: 6000,
				dialogTableVisible: false,
				WithdrawalShow: false,
				outerVisible: false,
				innerVisible: false,
				money: '',
				Withdrawal:'',
				//支付方式
				disabled: true,
				activeName: 'first',
				/* 支付提示 */
				hint: true,
				bank_card: [{
						zfbk: false,
						logo: require('../assets/images/logo.png'),
						text: '中国美惑银行',
					},

					{
						zfbk: false,
						logo: require('../assets/images/commodityCenter/zgyh.jpg'),
						text: '中国银行 ',
					},
					{
						zfbk: false,
						logo: require('../assets/images/commodityCenter/youzheng.jpg'),
						text: '中国邮政银行',
					},
					{
						zfbk: false,
						logo: require('../assets/images/commodityCenter/zg.jpg'),
						text: '中国工商银行',
					},
					{
						zfbk: false,
						logo: require('../assets/images/commodityCenter/zn.jpg'),
						text: '中国农业银行',
					},
				],
				paymentList: [{
						zfbk: false,
						logo: require('../assets/images/logo.png'),
						text: '美惑支付',
					},
					{
						zfbk: false,
						logo: require('../assets/images/commodityCenter/weixin.jpg'),
						text: '微信支付',
					},
					{
						zfbk: false,
						logo: require('../assets/images/commodityCenter/zhifubao.jpg'),
						text: '支付宝支付',
					},
				],
				ruleForm: {
					/* 姓名 */
					name: '',
					/* 身份证 */
					identity: '',
					/* 卡号 */
					card: '',
					/* 手机号码 */
					phone: '',
					/* 验证码 */
					yzm: '',
				},
				rules: {
					name: [{
							required: true,
							message: '请输入姓名',
							trigger: 'blur'
						},
						{
							min: 3,
							max: 5,
							message: '长度在 3 到 5 个字符',
							trigger: 'blur'
						}
					],
					identity: [{
							required: true,
							message: '请输入身份证',
							trigger: 'blur'
						},
						{
							min: 18,
							max: 18,
							message: '请输入正确身份证',
							trigger: 'blur'
						}
					],
					card: [{
							required: true,
							message: '请输入卡号',
							trigger: 'blur'
						},
						{
							min: 15,
							max: 19,
							message: '请输入正确卡号',
							trigger: 'blur'
						},
						/* {
						    type: 'number',
						    message: '卡号暂时只支持数字',
						    trigger: 'blur',
						} */
					],
					phone: [{
							required: true,
							message: '请输入手机号',
							trigger: 'blur'
						},
						{
							min: 11,
							max: 11,
							message: '请输入正确手机号',
							trigger: 'blur'
						},
						/* {
						    type: 'number',
						    message: '手机号暂时只支持数字',
						    trigger: 'blur',
						} */
					],
					yzm: [{
							required: true,
							message: '请输入验证码',
							trigger: 'blur'
						},

					],
				},
				//银行卡列表
				DCnum: 0,
				CCnum: 0,
				bankCardList: [{
						logo: 'zn.jpg',
						bankName: '中国农业银行',
						num: '6228480831781064918',
						type: '储蓄卡',
						userName: '云淡风清',
						phone: '18445156052',
						operation: false
					},
					{
						logo: 'zgyh.jpg',
						bankName: '中国银行',
						num: '6228480831781068644',
						type: '储蓄卡',
						userName: '云淡风清',
						phone: '17926564587',
						operation: false
					},
					{
						logo: 'zg.jpg',
						bankName: '中国工商银行',
						num: '6228480831781065241',
						type: '储蓄卡',
						userName: '云淡风清',
						phone: '18926568696',
						operation: false
					},
					{
						logo: 'youzheng.jpg',
						bankName: '中国邮政银行',
						num: '6217996100080404583',
						type: '信用卡',
						userName: '云淡风清',
						phone: '17926563245',
						operation: false
					}
				],
				// 银行卡添加
				bankCardAddShow: false,
			};
		},
		mounted() {
			// 银行卡数
			let DCnum = 0;
			let CCnum = 0;
			for (let i = 0; i < this.bankCardList.length; i++) {
				if (this.bankCardList[i].type == "储蓄卡") {
					DCnum++
				}
				if (this.bankCardList[i].type == "信用卡") {
					CCnum++
				}
			}
			this.DCnum = DCnum;
			this.CCnum = CCnum;
		},
		methods: {
			// 下一步
			nextStep() {
				let reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
				if (reg.test(this.money) && parseFloat(this.money) != 0) {
					this.innerVisible = true;
				} else {
					Notification.error({
						title: '请输入正确金额',
						showClose: false,
						duration: 3000,
					});
				}
			},
			// 立即充值
			rechargeNow() {
				this.innerVisible = false;
				this.dialogTableVisible = false;
				this.allMoney = parseFloat(this.money) + parseFloat(this.allMoney);
				Message.success('充值成功');
			},
			// 立即提现
			WithdrawalNow() {
				let reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
				if (reg.test(this.Withdrawal) && parseFloat(this.Withdrawal) != 0 && !(this.Withdrawal>this.allMoney)) {
					this.WithdrawalShow = false;
					this.allMoney = parseFloat(this.allMoney) - parseFloat(this.Withdrawal);
					Message.success('提现成功');
				} else {
					Notification.error({
						title: '请输入正确金额',
						showClose: false,
						duration: 3000,
					});
				}
				
			},
			// 支付方式
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						Message.success('添加成功');
						this.bankCardAddShow = false;
					} else {
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},

			choice(k) {
				this.disabled = false /* 支付按钮 */
				this.hint = false /* 关闭支付提示 */
				/* 选卡支付 */
				for (let i = 0; i < this.bank_card.length; i++) {
					if (k == i) {
						this.bank_card[k].zfbk = true;
					} else {
						this.bank_card[i].zfbk = false;
						/* 平台 */
						for (let j = 0; j < this.paymentList.length; j++) {
							this.paymentList[j].zfbk = false;
						}
					}
				}
			},
			terrace(k) {
				this.disabled = false /* 支付按钮 */
				this.hint = false /* 关闭支付提示 */
				for (var i = 0; i < this.paymentList.length; i++) {
					if (k == i) {
						this.paymentList[k].zfbk = true;
					} else {
						this.paymentList[i].zfbk = false;
						for (let j = 0; j < this.bank_card.length; j++) {
							this.bank_card[j].zfbk = false;
						}
					}
				}

			},
			// 鼠标移入事件
			mouseOver(k) {
				for (let i = 0; i < this.bankCardList.length; i++) {
					if (i == k) {
						this.bankCardList[k].operation = true;
					} else {
						this.bankCardList[i].operation = false;
					}
				}
			},
			// 鼠标移出事件
			mouseLeave() {
				for (let i = 0; i < this.bankCardList.length; i++) {
					this.bankCardList[i].operation = false;
				}
			},
			// 删除地址
			deleBankCard(k) {
				MessageBox.confirm('您确定要删除该银行卡吗？', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning',
					center: true,
					lockScroll: false,
					closeOnClickModal: false,
				}).then(() => {
					this.bankCardList.splice(k, 1);
					let DCnum = 0;
					let CCnum = 0;
					for (let i = 0; i < this.bankCardList.length; i++) {
						if (this.bankCardList[i].type == "储蓄卡") {
							DCnum++
						}
						if (this.bankCardList[i].type == "信用卡") {
							CCnum++
						}
					}
					this.DCnum = DCnum;
					this.CCnum = CCnum;
					Message.success('删除成功!');
				}).catch(() => {

				});
			},
		}
	}
</script>

<style scoped>
	.my_account .el-main {
		display: flex;
		justify-content: center;
		text-align: left;
		padding-top: 50px;
		background-color: #f5f7fa;
	}

	.el-submenu .el-menu-item {
		min-width: 0;
	}

	/* 右边 */
	.el-main .right {
		width: 860px;
		/* border: 1px solid firebrick; */
		margin-left: 20px;
	}

	/* 余额 */
	.right .balance {
		padding: 25px 20px;
		background-color: #fff;
		margin-bottom: 30px;
		display: flex;
		align-items: center;
	}

	.balance .title {
		font-size: 18px;
