        steps,
        step,
        divider,
        row,
        col,
        InputNumber,
        checkbox,
        input,
        button,
        dialog,
        table,
        TableColumn,
        form,
        FormItem,
        select,
        option
    } from 'element-ui';
    export default {
        data() {
            return {
                /* 是否显示所有地址 */
                daa: true,
                active: 0,
                num: 1,
                price: 215,
                total: 215,
                checked: false,
                checked1: false,

                textarea: '',
                dialogFormVisible: false,
                form: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: ''
                },
                formLabelWidth: '120px',
                userinfo: [{
                        reveal: true,
                        modification: true,
                        a: true,
                        consignee: '广西南宁（路人憨憨收）',
                        shipping: '西乡塘心圩丰达路8号217青年公寓高新店 15077399401',
                    },
                    {
                        reveal: true,
                        modification: false,
                        a: false,
                        consignee: '广西南宁（路人憨憨收）',
                        shipping: '西乡塘心圩丰达路8号217青年公寓高新店 15077399401',
                    },
                    {
                        reveal: true,
                        modification: false,
                        a: false,
                        consignee: '广西南宁（路人憨憨收）',
                        shipping: '西乡塘心圩丰达路8号217青年公寓高新店 15077399401',
                    },
                    {
                        reveal: false,
                        modification: false,
                        a: false,
                        consignee: '广西南宁（路人憨憨收）',
                        shipping: '西乡塘心圩丰达路8号217青年公寓高新店 15077399401',
                    },
                    {
                        reveal: false,
                        modification: false,
                        a: false,
                        consignee: '广西南宁（路人憨憨收）',
                        shipping: '西乡塘心圩丰达路8号217青年公寓高新店 15077399401',
                    },
                ],

            };
        },
        components: {
            headerNav,
            [container.name]: container,
            [header.name]: header,
            [main.name]: main,
            [steps.name]: steps,
            [step.name]: step,
            [divider.name]: divider,
            [row.name]: row,
            [col.name]: col,
            [InputNumber.name]: InputNumber,
            [checkbox.name]: checkbox,
            [input.name]: input,
            [button.name]: button,
            [dialog.name]: dialog,
            [table.name]: table,
            [TableColumn.name]: TableColumn,
            [form.name]: form,
            [FormItem.name]: FormItem,
            [select.name]: select,
            [option.name]: option,

        },
        methods: {
            /* 总价 */

            handleChange() {
                let num = this.num

                let price = this.price
                this.total = price * num
            },
            /* 选择地址 */
            choice(k) {
                this.modification = true;
                for (var i = 0; i < this.userinfo.length; i++) {
                    if (k == i) {
                        this.userinfo[k].a = true;
                        this.userinfo[k].modification = true;
                    } else {
                        this.userinfo[i].a = false;
                        this.userinfo[i].modification = false;
                    }
                }
            },
            /* 显示所有地址 */
            dizhi() {
                this.daa = false;
                for (var i = 0; i < this.userinfo.length; i++) {
                    this.userinfo[i].reveal = true;
                }
            },
            /* 收起地址 */
            hta() {
                this.daa = true;
                for (var i = 0; i < this.userinfo.length; i++) {
                    if (i > 2) {
                        this.userinfo[i].reveal = false;
                    }
                }
            },
            submit(){
                 this.$router.push('/commodity_payment')
            }
        }
    }
</script>

