<template>
	<div id="header">
		<el-header class="">
			<el-row class="container">
				<el-col :span="3">
					<div class="grid-content bg-purple">
						<h1 class="logo">
							<a href="">美惑</a>
						</h1>
					</div>
				</el-col>
				<el-col :span="10">
					<div class="grid-content bg-purple-light">
						<nav class="index_nav">
							<router-link to="/index">首页</router-link>
							<router-link to="/Tourism">旅行社区</router-link>
							<router-link to="/commodity_index">商品中心</router-link>
							<router-link to="/AboutUs">关于我们</router-link>
							<router-link to="/Download">app下载</router-link>
						</nav>
					</div>
				</el-col>
				<el-col :span="7">
					<div class="grid-content bg-purple"></div>
				</el-col>
				<el-col :span="4">
					<div class="grid-content bg-purple  loginBox">
						<router-link to="/shopCart">
							<el-badge :value="$store.getters.getAllCount" class="item">
								<i class="el-icon-shopping-cart-2" style="font-size:20px;cursor: pointer;"></i>
							</el-badge>
						</router-link>
						<div class="login ">
							<!-- <el-avatar :size="30" :src="circleUrl"></el-avatar> -->
							<el-dropdown v-if="loginK" trigger="click">
								<el-avatar :size="30" :src="circleUrl"></el-avatar>
								<el-dropdown-menu slot="dropdown">
									<router-link to="/my_account">
										<el-dropdown-item icon="el-icon-coin">我的账户</el-dropdown-item>
									</router-link>
									<router-link to="/my_order">
										<el-dropdown-item divided icon="el-icon-tickets">我的订单</el-dropdown-item>
									</router-link>
									<router-link to="/my_set">
										<el-dropdown-item divided icon="el-icon-setting">个人设置</el-dropdown-item>
									</router-link>
									<span @click="outLogin()">
										<el-dropdown-item divided icon="el-icon-switch-button">退出登录</el-dropdown-item>
									</span>
								</el-dropdown-menu>
							</el-dropdown>
							<router-link  v-else to="/login">
								<span>你好，请登录</span>
							</router-link>
							<!-- <span>登录</span>/<span>注册</span> -->
						</div>
					</div>
				</el-col>
			</el-row>
		</el-header>
	</div>
</template>
<script>
	import {
		Header,
		Footer,
		Row,
		Col,
		Icon,
		Avatar,
		Badge,
		Dropdown,
		DropdownMenu,
		DropdownItem
	} from 'element-ui';
	export default {
		name: 'headerNav',
		components: {
			[Header.name]: Header,
			[Footer.name]: Footer,
			[Row.name]: Row,
			[Col.name]: Col,
			[Icon.name]: Icon,
			[Avatar.name]: Avatar,
			[Badge.name]: Badge,
			[Dropdown.name]: Dropdown,
			[DropdownMenu.name]: DropdownMenu,
			[DropdownItem.name]: DropdownItem,
		},
		data() {
			return {
				circleUrl: require('../assets/images/touxiang.jpg'),
				loginK: '',
			}
		},
		created() {
			this.loginK = sessionStorage.getItem("loginK");
		},
		methods:{
			outLogin(){
				sessionStorage.removeItem("loginK");
				this.$router.push('/index');
				location.reload();
			}
		}
	}
</script>
<style>
	#header {
		width: 100%;
		background-color: #fff;
		text-align: center;
		position: fixed;
		top: 0px;
		left: 0;
		transition: all 0.2s ease-in 0s;
		z-index: 999;
		border-bottom: 1px solid #f5f5f5;
	}

	#header .el-header {
		color: #333;
		text-align: center;
		line-height: 60px;
		padding: 0;
		font-weight: bold;
		color: #57BEC3;
		/* border-bottom:1px solid #f5f5f5; */
	}

	#header .el-row {
		margin-bottom: 20px;
	}

	#header .el-row:last-child {
		margin-bottom: 0;
	}

	#header .grid-content {
		border-radius: 4px;
		min-height: 60px;
		text-align: left;
	}

	#header .grid-content .logo a {
		width: 60px;
		height: 50px;
		background: url("../assets/images/logo.png");
		background-size: 50px 50px;
		background-repeat: no-repeat;
		display: inline-block;
		vertical-align: middle;
		text-indent: -999px;
	}

	#header .index_nav {
		display: flex;
		justify-content: space-between;
		text-align: center;
	}

	#header .index_nav a {
		width: 80px;
		height: 60px;

	}

	#header .index_nav a:hover {
		width: 80px;
		height: 60px;
		font-size: 18px;
		background: linear-gradient(to top, #57BEC350, #fff);
	}

	#header .loginBox {
		text-align: right;
	}

	#header .login {
		margin-left: 30px;
		display: inline-block;
		cursor: pointer;
	}

	#header .loginBox * {
		vertical-align: middle;
	}

	#header .loginBox .el-badge__content.is-fixed {
		top: 21px;
	}

	#header .loginBox .el-badge__content {
		background-color: #f56c6c00;
		color: #F56C6C;
		font-size: 10px;
		padding: 0 1px;
		border: 0px solid #FFF;
	}

	/* .item {
  margin-top: 10px;
  margin-right: 40px;
} */
</style>
