for (var proKey in areaList.province_list) {
	var proList = {
		value: areaList.province_list[proKey],
		label: areaList.province_list[proKey],
		children: []
	}
	var citList = {};
	for (var citKey in areaList.city_list) {
		if (proKey.substr(0, 2) == citKey.substr(0, 2)) {
			citList.value = areaList.city_list[citKey];
			citList.label = areaList.city_list[citKey];
			citList.children = [];
			proList.children.push(citList);
			var countList = {};
			for (var couKey in areaList.county_list) {
				if (citKey.substr(0, 4) == couKey.substr(0, 4)) {
					countList.value = areaList.county_list[couKey];
					countList.label = areaList.county_list[couKey];
					citList.children.push(countList);
					countList = {};
				}
			}
			citList = {};
		}
		
	}
	options.push(proList);
}


